<template>
  <div>
    <div class="logo">
      <img src="/uploads/images/logo/logo_web.jpg" alt="">
    </div>
    <div class="message">
      <div class="tel">
        <div class="left">
          <input v-model="phone" placeholder="请输入手机号码" type="number" min="0">
        </div>
        <!-- <div @click="select()" class="right">
          <span class="s">|</span>
          <span class="red">选择机构</span>
        </div> -->
      </div>
      <div class="pwd">
        <div class="left">
          <input v-model="pwd" placeholder="请输入密码" type="password">
        </div>
      </div>
    </div>
    <div @click="submit()" class="login">登录</div>
    <div class="forget">
      <span @click="forget()" class="left">忘记密码？</span>
      |
      <router-link tag="span" to="/loginRegistrat/registrat" class="right">现在注册  ></router-link>
    </div>
    <div class="model" v-show="modelShow">
      <div class="box">
        <div class="top">选择登录会员单位</div>
        <div class="bottom">
          <p @click="selectBesure()">测试专用</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Axios from '../../common/js/getAxios'
import {mapMutations} from 'vuex'
import {mapGetters} from 'vuex'
export default {
  data(){
    return {
      modelShow : false,
      phone : "",
      pwd : ""
    }
  },
  computed : {
     ...mapGetters([
       'Authorization'
    ]),    
  },
  methods : {
    // 控制vuex
    ...mapMutations({
      setAuthorization : "SET_AUTHORIZATION" 
    }),      
    submit(){
      Axios.postAxios('/api/authorizations',{
        phone : this.phone,
        password : this.pwd
      }).then((res)=>{     
        // if(res.data.status_code == 401){
        //    this.$message("请检查您的用户名或密码是否正确");
        //   return false;
        // }   
        this.setAuthorization(res.data.access_token);        
        //将token存储到本地
        localStorage.setItem("Authorization",this.Authorization);
        //路由跳转到用户页
        this.$router.push("/me");
      },(err)=>{
         this.$message("请检查您的用户名或密码是否正确");
        console.log(err.response);
      })      
    },
    forget(){
      this.$router.push('/loginRegistrat/forget')
    },
    select(){
      this.modelShow = true;
    },
    selectBesure(){
      this.modelShow = false;
    }
  }
}
</script>


<style lang="stylus" scoped>
@import '~@/assets/css/loginRegistrat/login'
  .model{
  position: fixed
  top: 0
  bottom: 0
  left: 0
  right: 0
  background: rgba(0,0,0,.3)
  .box{
    border-radius 6px;
    width 6rem    
    position absolute
    height auto
    top 50%
    transform translateY(-50%)
    left 0
    right 0
    margin auto
    .top{
      height .8rem
      border-radius 6px 6px 0 0;
      background #fc5155
      color #fff
      line-height .8rem
      text-indent 2em
      width 6rem
    }
    .bottom{
      width 6rem
      background #FFF
      padding-bottom .2rem
      border-radius 0 0 6px 6px
      @extend .nobefore
      p{
        width 5.4rem
        height .82rem
        margin .2rem auto 0
        border 1px solid #eaeaea
        line-height .82rem
        text-indent 2em
        font-size .26rem
        color #303030
      }
    }
  }
}
</style>




