
<template>
    <div>
        <return-top>
          <p slot="title">添加银行卡</p>
        </return-top>
        <div class="order">
            <span class="qes">省份 城市</span>
            <div  @click="showSelect" class="a">
              <el-input placeholder="请选择地址" v-model="cityEx" >
              </el-input>
            </div>
        </div>

        <div class="order">
            <span class="qes">身份证号码</span>
            <input type="text" class="a"> 
        </div>

        <div class="order">
            <span class="qes">银行卡号</span>
            <input type="text" class="a"> 
        </div>

        <div class="order">
            <span class="qes">支行名称</span>
            <input type="text" class="a"> 
        </div>

        <div class="order">
            <span class="qes">预留手机</span>
            <input type="text" class="a"> 
        </div>

        <div @click="submitNext()" class="submit">
            保存
        </div>
    </div>
</template>

<script>
import ReturnTop from "@/components/ReturnTop";
import '@/../static/iosselect.css'
import '@/../static/iosSelect.js'
import '@/../static/zepto.js'
import {iosProvinces, iosCitys, iosCountys} from '@/../static/areaData_v2.js'
export default {
  data() {
    return {
      //默认选择城市
      cityEx:'北京市 北京市', 
      payIndex: 0,
      //支付方法
      payChoice: [
        {
          title: "快捷支付",
          img: "../../../static/images/me/pay_img.jpg",
          choiceOk: "&#xe62b;"
        },
        {
          title: "银直联快捷支付",
          img: "../../../static/images/me/pay_img.jpg",
          choiceOk: "&#xe60f;"
        }
      ]
    };
  },
  components: {
    ReturnTop
  },
  methods: {
    //地址显示控件点击后触发事件
    showSelect:function(){
      //自己实现的根据地址名称获取id，代码如下
      var levels = this.textToLevel(this.cityEx)
      var iosSelect = new IosSelect(3, 
          [iosProvinces, iosCitys, iosCountys],
          {
              title: '地址选择',
              itemHeight: 35,
              relation: [1, 1],
              //三个初始值，省市县
              oneLevelId: levels[0],
              twoLevelId: levels[1],
              threeLevelId: levels[2],
              //地址选择点击确定后的回调
              callback: (selectOneObj, selectTwoObj, selectThreeObj)=> {
                this.cityEx = selectOneObj.value + ' ' + selectTwoObj.value
                console.log(this.cityEx)
              }
      });
    }, 
//根据地址名字转化未三级level，用以给iosSelect设置初始值
    textToLevel:function(text){
      var arr = []
      if (text) {
        var objs = text.split(' ')

        if (objs[0]) {
          iosProvinces.forEach((item, index)=>{
            if (item.value == objs[0]) {
              arr.push(item.id)
            }
          })
        }

        if (objs[1]) {
          iosCitys.forEach((item, index)=>{
            if (item.value == objs[1]) {
              arr.push(item.id)
            }
          })
        }

        if (objs[2]) {
          iosCountys.forEach((item, index)=>{
            if (item.value == objs[2]) {
              arr.push(item.id)
            }
          })
        }
      }

      return arr

    },
    select(index) {
      //去除其他选中状态
      this.payChoice.forEach(item => {
        item.choiceOk = "&#xe60f;";
      });
      this.payChoice[index].choiceOk = "&#xe62b;";
      this.payIndex = index;
    },
    submitNext() {
      this.$router.push({
        path: "third"
      });
    }
  }
};
</script>
<style lang="stylus" scoped>
@import '~@/assets/css/me/third';

</style>

<style lang="stylus">
#threeLevelContain{
  display none
}
#oneLevelContain,#twoLevelContain{
  width 50%
}
.el-input{
  width 5.2rem
  position absolute
  top 0
}
.ios-select-widget-box header.iosselect-header a{
  color #0084ff
}
.el-input__inner{
  font-size: .28rem
  color: #333333
  border-bottom: 1px solid #e5e5e5
  line-height: .8rem
  border none
  background none
}
</style>





