<template>
    <div class="return_header">
        <div @click="back()" class="return">
            <span class="iconfont">&#xe6a0;</span>
        </div>
        <span class="txt">返回</span>
        <p @click="back()" class="title">
            <slot name="title"></slot>
        </p>
    </div>
</template>

<script>
export default {
    methods : {
        back(){
            this.$router.go(-1)
        }
    }
}
</script>
<style lang="stylus" scoped>
@import '~@/common/css/variable'
    .return_header{
        overflow hidden
        padding-left .18rem
        background #000000
        height .88rem
        line-height .88rem
        color #ffffff
        position relative
        .return{
            float left
            height 100%
            .iconfont{
                font-size .4rem
                line-height .88rem
            }
        }
        .txt{
            font-size .28rem
            line-height .88rem
        }
        .title{
            position absolute
            top 0
            font-size .36rem
            text-align center
            display block
            width 100%
            padding-right .18rem
        }
    }
</style>




