<template>
  <div>
    <div style="height:auto;" class="title_box">
      <div
        style="background:#000;position:relative;"
        class="return"
        @click="back()"
      >
        <div class="a">
          <span class="iconfont">&#xe6a0;</span>
        </div>
        <span class="txt">返回</span>
        <span class="cl">关注牛人</span>
      </div>
    </div>

    <ul v-for="(item,index) in bullMsg" :key="index" class="users">
      <router-link tag="li" :to="{path:'/bullUser',query:{id:v}}">
        <div class="user_info">
          <div class="icon">
            <img
              src="../../../static/images/default-user-img.png"
              alt="用户头像"
            >
          </div>
          <div class="user">
            <p class="top">{{item.name}}</p>
            <p class="bottom">简介：{{item.intro}}</p>
          </div>
        </div>

        <div class="more_news">
          <ul>
            <li>
              <p class="top">策略数</p>
              <p class="bottom">{{item.tacticsnum}}</p>
            </li>
            <li>
              <p class="top">胜算率</p>
              <p class="bottom">{{item.winsum/item.tacticsnum}}</p>
            </li>
            <li>
              <p class="top">收益率</p>
              <p class="bottom">{{item.yieldrate}}</p>
            </li>
            <li>
              <p class="top">粉丝数</p>
              <p class="bottom">{{item.follower}}</p>
            </li>
          </ul>
        </div>
      </router-link>
    </ul>

  </div>
</template>

<script>
import Axios from "../../common/js/getAxios";

export default {
  data() {
    return {
      bullMsg : []
    };
  },
  created(){
    this.getBull();
  },
  methods: {
    back() {
      this.$router.go(-1);
    },
    getBull(){
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosGet(
        "/api/myfocus",
        {
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          this.bullMsg = res.data.data;          
        },
        err => {}
      );
    }
  }
};
</script>

<style lang="stylus" scoped>
@import '~@/assets/css/home/bullUser';
.cl {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  text-align: center;
  font-size: 0.36rem;
}
.user_info .user .top, .user_info .user .bottom, .more_news ul li {
  color: #000;
  opacity: 1;
}
.more_news {
  padding: 0;
  margin-top: 0.1rem;
}
.users{
    margin-bottom :.2rem;
    padding-bottom .2rem;
    border-bottom 1px solid #ccc;
}
</style>

 


