<template>
  <div>
    <h1 style="text-align:center;margin-bottom:.3rem;">请输入充值金额</h1>
    <van-cell-group style="width:6.6rem;box-shadow: rgb(165, 165, 165) 0px 0px 3px 0px;margin: 0 auto;">
      <van-field style="font-size:.3rem" input-align="center" v-model="sms" center clearable placeholder="请输入充值金额(元)">
      </van-field>
    </van-cell-group>
    <van-button @click="pay" style="margin:.6rem auto;display:block;width:6rem" type="warning">充值</van-button>
    <h1 style="width:96%;margin:.4rem auto;font-size:.3rem;text-align:center;">      
            <p style="color:#df411e" class="bottom">周一至周日（7*24小时实时入金）</p>
            <!-- <p style="color:#df411e" class="bottom">转账时请备注用户名和转账金额</p> -->
        </h1>
  </div>
</template>

<script>
import Axios from "../../common/js/getAxios";
export default {
    data(){
        return{
            sms:""
        }
    },
  methods: {
    pay() {
      let userToken = localStorage.getItem("Authorization");
      Axios.postAxios(
        "/api/epay",
        {
          money: this.sms
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(res => {
        window.location.href = res.data.data.url;
        // console.log(res)
      },err=>{
        this.$message.error("充值失败")
      });
    }
  }
};
</script>

<style>
</style>



