<template>
  <div>
    <return-top>
      <p slot="title">创建策略</p>
    </return-top>
    <div class="share_box">
      <ul class="ul1">
        <li class="first">
          <p class="top">{{share[1]}}</p>
          <p class="bottom">{{share[0]}}</p>
        </li>
        <li class="secend">
          <p :class="{down_green:(share[4]-share[3])/share[3]<0}" class="top">{{parseFloat(share[4]).toFixed(2)}}</p>
          <div class="bottom">
            <span :class="{down_green:(share[4]-share[3])/share[3]<0}" class="left">{{(share[4]-share[3]).toFixed(2)}} &nbsp;</span>
            <span :class="{down_green:(share[4]-share[3])/share[3]<0}" class="right">{{(share[4]-share[3])/share[3] | toPercentage}}</span>
          </div>
        </li>
        <!-- <li class="third">行情</li> -->
        <!-- 将code传递过去 -->
        <router-link tag="div" :to="{path:'/Ks/',query : {code:code}}" style="color:red;" class="third">行情<span style="font-size:.12rem;margin-left:.1rem" class="iconfont">&#xe631;</span></router-link>
      </ul>
      <ul class="ul2">
        <li>市价委托</li>
        <li class="secend">{{parseFloat(share[4]).toFixed(2)}}</li>
      </ul>
    </div>
    <div class="select_box1">
      <ul class="title">
        <li>选择信用金（元）</li>
        <li style="color : #fff">1</li>
        <li class="third"><input type="text" v-model="selectaddxinyongjin"></li>

      </ul>
      <ul class="select_num">
        <li class="left">
          你也可选择
        </li>
        <li class="right">
          <ul>
            <li @click="changeNum(index)" :class="{nomargin:index%4 == 3,active : index == selectNumIndex}" v-for="(item,index) in selectNum" :key="index">{{item}}</li>
          </ul>
        </li>
      </ul>
    </div>
    <div class="select_box2">
      <ul class="ul1">
        <li class="first">
          策略匹配
        </li>
        <li class="secend">
          <ul>
            <li @click="changeMultiple(index)" :class="{nomargin:index%3 == 2,active : index == selectMultipleIndex}" v-for="(item,index) in selectMultiple" :key="index">{{item}}倍</li>
          </ul>
        </li>
        <li class="third">
          <!-- 信用金*倍数/股价 -->
          <!-- <p class="top">{{Math.floor(selectaddxinyongjin*parseInt(selectMultiple[selectMultipleIndex])/parseInt(share[4]))}}股</p> -->
          <p class="top">{{num}}股</p>
          <p class="bottom">值{{Math.round(num * share[4])}}元</p>
        </li>
      </ul>
    </div>
    <div class="select_box3">
      <ul class="ul1">
        <li class="first">
          止盈
        </li>
        <li class="secend">
          <div @click="reduce1()" class="reduce">-</div>
          <input v-model="addReduceNum" type="number" class="number">
          <div @click="add1()" class="add">+</div>
        </li>
        <li class="third">
          涨幅约<span class="red">{{(addReduceNum-parseFloat(share[4]))/parseFloat(share[4]) | toPercentage}}</span>
        </li>
      </ul>
      <ul class="ul1">
        <li class="first">
          止损
        </li>
        <li class="secend">
          <div @click="reduce2()" class="reduce">-</div>
          <input v-model="lessReduceNum" type="number" class="number">
          <div @click="add2()" class="add">+</div>
        </li>
        <li class="third">
          跌幅约<span class="red down_green">{{(lessReduceNum-parseFloat(share[4]))/parseFloat(share[4]) | toPercentage}}</span>
        </li>
      </ul>
    </div>
    <div class="select_box4">
      <div class="first">建仓费</div>
      <div class="secend">{{buildCost}}元<span v-show="buildCost == '' ">请选择信用金与策略匹配来计算仓费</span></div>
    </div>
    <div class="select_box5">
      <div class="select_box4">
        <div class="first">递延费</div>
        <div class="secend">{{deferred}}/天</div>
      </div>
      <div class="select_box4">
        <div class="first">是否递延</div>
        <div class="secend special">若未开启自动递延<br />到期日 14:40 将卖出股票</div>
        <div class="ydf">
          <el-switch active-color="#ff5a5a" class="ydf" v-model="ydf">
          </el-switch>
        </div>
      </div>
      <div class="select_box4">
        <div class="first">追加信用金</div>
        <div class="secend special">开启为自动<br />关闭为手动</div>
        <div class="ydf">
          <el-switch active-color="#ff5a5a" class="ydf" v-model="zj">
          </el-switch>
        </div>
      </div>
    </div>
    <div class="should_pay">
      <div class="left">
        <p class="top">需支付<span>{{parseFloat(buildCost)+parseFloat(selectNum[selectNumIndex])}}</span>元</p>
        <p class="bottom">（账户余额{{myMoney}}元）</p>
      </div>
      <div v-if="isdanger!=1" @click="sendCreate()" class="right">马上创建</div>
      <div v-else class="right" style="background:#ccc;color:#fff">马上创建</div>
    </div>
    <div @click="agreeClick()" class="agree">
      <i v-show="agree" class="iconfont">&#xe632;&nbsp;&nbsp;</i>
      <i v-show="!agree" class="iconfont">&#xe60f;</i>
      <span class="txt">我已阅读并同意<router-link tag="span" targe="_blank" to="/payMessage">《支付服务协议》</router-link></span>
      <!-- <span class="right">帮助说明</span> -->
    </div>
  </div>
</template>

<script>
import ReturnTop from "@/components/ReturnTop";
import Axios from "../../common/js/getAxios";
export default {
  filters: {
    //转成百分数
    toPercentage(value) {
      let str = Number(value * 100).toFixed(2);
      str += "%";
      return str;
    }
  },
  mounted() {
      this.getInit()
    //获取策略倍数
    this.getMultiple();
    //获取股票信息
    this.getShare();
    //获取建仓费,递延费，2018/11/3，懒得仔细看，反正打开了就报错
    // this.getCDMoney();
    //获取用户信息（余额）
    this.getUserInfo();
  },
  watch: {
    selectaddxinyongjin(v) {
      let state = false;
      for (var i = 0; i < this.selectNum.length; i++) {
        if (v == this.selectNum[i]) {
          this.selectNumIndex = i;
          state = true;
        }
      }
      if (state == false) {
        this.selectNumIndex = -1;
      }
      this.getCDMoney();
    }
  },
  data() {
    return {
      //上个页面的id
      id : this.$route.query.id,
      //获取来的最低止损
      // getLessNum : '',
      //是否风险
      isdanger: "",
      //余额
      myMoney: 0,
      //是否同意支付协议
      agree: true,
      //最低止损价
      minLess: 0,
      //最低止盈价
      minAdd: 0,
      // 需要交纳的递延费
      deferred: "",
      //建仓费
      buildCost: "",
      //股票信息
      share: [],
      //上个页面传递过来的股票代码,通过这个查询更多信息
      code: this.$route.query.code,
      //是否开启递延
      ydf: true,
      //是否追加信用金
      zj: false,
      //止盈
      addReduceNum: 1,
      //止损
      lessReduceNum: "",
      //   选择信用金索引
      selectNumIndex: 0,
      //选择倍数索引
      selectMultipleIndex: 0,
      //信用金
      selectNum: [1000, 5000, 10000, 20000, 30000, 50000, 80000, 100000],
      //手动输入信用金
      selectaddxinyongjin: 1000,
      selectMultiple: ["", "", ""],
      is_add: 1,
      is_sell: 1,



      //牛人的信用金
      bullXyj : 0,
      //牛人的策略
      bullCl : 0
    };
  },
  components: {
    ReturnTop
  },
  computed: {
    num: function() {
      let benjin =
        this.selectaddxinyongjin *
        this.selectMultiple[this.selectMultipleIndex];
      return Math.round(benjin / this.share[4]) > 100
        ? Math.floor(Math.round(benjin / this.share[4]) / 100) * 100
        : 100;
    }
  },
  methods: {
      //新的获取所有信息方式
      getInit(){
      let userToken = localStorage.getItem("Authorization");
          Axios.postAxios(
        "/api/newfollowbuy",
        {
          id : this.id
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          this.bullXyj = res.data.data.xingyongjin;
          this.bullCl = res.data.data.multiple;
          switch(this.bullCl){
            case 8:this.selectMultipleIndex = 0;break;
            case 5:this.selectMultipleIndex = 1;break;
            case 3:this.selectMultipleIndex = 2;break;
          }
          this.selectaddxinyongjin = this.bullXyj;
        },
        err => {
          console.log(err.response);
        }
      );
      },
    //是否同意支付协议
    agreeClick() {
      this.agree = !this.agree;
    },
    //   发送创建请求
    sendCreate() {
      if (!this.agree) {
         this.$message("请先阅读并同意支付协议后再创建订单");
        return false;
      }
      let userToken = localStorage.getItem("Authorization");

      if (this.ydf) {
        this.is_sell = 2;
      } else {
        this.is_sell = 1;
      }
      if (this.zj) {
        this.is_add = 2;
      } else {
        this.is_add = 1;
      }
      Axios.postAxios(
        "/api/tactics",
        {
          code: this.code,
          price: this.share[4],
          xingyongjin: this.selectaddxinyongjin,
          multiple: parseInt(this.selectMultiple[this.selectMultipleIndex]),
          max_price: this.addReduceNum,
          min_price: this.lessReduceNum,
          create_money:
            parseFloat(this.buildCost) +
            parseInt(this.selectNum[this.selectNumIndex]),
          is_add: this.is_add,
          is_sell: this.is_sell,
          is_analog: 0
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          if (res.data.status == true) {
             this.$message("订单创建成功！");
             this.$router.push('/cl')
          } else {
            // console.log(res.data.msg)
             this.$message(res.data.msg);
          }
        },
        err => {
          console.log(err.response);
        }
      );
    },
    // 获取用户信息
    getUserInfo() {
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosGet(
        "/api/user/",
        {},
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          if (res) {
            this.myMoney = res.data.data.money;
          } else {
             this.$message("登录失败，请重新登录");
          }
        },
        err => {
          //  this.$message("请登录查看更多页面");
        }
      );
    },
    //   获取策略倍数（后台配置）
    getMultiple() {
      Axios.axiosGet("/api/multiple", {}, {}).then(
        res => {
          if (res.data.status) {
            this.selectMultiple = res.data.data;
            this.getCDMoney();
          } else {
          }
        },
        err => {
          console.log(err);
        }
      );
    },
    //获取递延费
    getCDMoney() {      
      let userToken = localStorage.getItem("Authorization");
      Axios.postAxios(
        "/api/tactics/procedure",
        {
          code: this.code,
          xingyongjin: this.selectaddxinyongjin,
          multiple: parseInt(this.selectMultiple[this.selectMultipleIndex])
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {          
          console.log(res.data.data)
          this.deferred = res.data.data.diyanfei;
          this.buildCost = res.data.data.cangfei;
          this.lessReduceNum = res.data.data.min_min_price;
        },
        err => {
          console.log(err);
        }
      );
    },
    //切换信用金
    changeNum(index) {
      this.selectNumIndex = index;
      this.selectaddxinyongjin = this.selectNum[index];
    },
    //   切换倍数
    changeMultiple(index) {
      this.selectMultipleIndex = index;
      this.getCDMoney();
      //重新获取止盈止损
      this.getShare();
    },
    //   获取建仓费
    getBuild() {
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosGet(
        "/api/tactics/storage/",
        {
          multiple: parseInt(this.selectMultiple[this.selectMultipleIndex]),
          xingyongjin: this.selectaddxinyongjin
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          console.log(res);
          if (res.data.status) {
            this.buildCost = res.data.data.cangfei;
          } else {
            (this.buildCost = ""),  this.$message("仓费获取出错");
          }
        },
        err => {
          console.log(err);
        }
      );
    },
    getShare() {
      Axios.axiosGet("/api/stock/getprice/", { code: this.code }).then(
        res => {          
          this.isdanger = res.data.data.is_danger;
          this.share = res.data.data[0];
          //请求成功后需要取得止损价格(当前价-当前价*0.8/倍数)
          let tmpZs = (
            parseFloat(this.share[4]) -
            parseFloat((parseFloat(this.share[4]) * 0.8).toFixed(2) + 0.01) /
              parseInt(this.selectMultiple[this.selectMultipleIndex])
          ).toFixed(2);
          // this.lessReduceNum = tmpZs;
          this.minLess = this.lessReduceNum;
          //取得最低止盈价格
          this.addReduceNum = (
            parseFloat(parseFloat(this.share[4]).toFixed(2)) * 1.1
          ).toFixed(2);
          //最低止盈点
          this.addReduceNumeff = (
            parseFloat(parseFloat(this.share[4]).toFixed(2)) + 0.01
          ).toFixed(2);
          this.minAdd = this.addReduceNum;
        },
        err => {
          console.log(err);
        }
      );
    },
    reduce1() {
      this.addReduceNum = (parseInt(this.addReduceNum * 100) - 1) / 100;
      if (this.addReduceNum < this.addReduceNumeff) {
         this.$message("您的止盈设置不能低于" + this.addReduceNumeff);
        this.addReduceNum = this.addReduceNumeff;
      }
    },
    add1() {
      this.addReduceNum = (parseInt(this.addReduceNum * 100) + 1) / 100;
    },
    reduce2() {
      this.lessReduceNum = (parseInt(this.lessReduceNum * 100) - 1) / 100;
      if (this.lessReduceNum < this.minLess) {
         this.$message("您的止损设置不能低于" + this.minLess);
        this.lessReduceNum = this.minLess;
      }
    },
    add2() {
      this.lessReduceNum = (parseInt(this.lessReduceNum * 100) + 1) / 100;
    }
  }
};
</script>

<style lang="stylus" scoped>
@import '~@/assets/css/self/create';
</style>




