<template>
  <div>
    <div class="user">
      <ul>
      <router-link to="/recharge" tag="li" class="pay pay_in">
          <img src="../../../static/images/in_money.png" alt="">
          <p>充值</p>
        </router-link>
      
        <li @click="login()" style="width: 1.28rem;" class="user_icon">
          <img :src="userimg" alt="">
          <p class="big">{{username}}</p>
        </li>
        <router-link to="/charge" tag="li" class="pay pay_out">
          <img src="../../../static/images/out_money.png" alt="">
          <p>提现</p>
        </router-link>
      </ul>
      <router-link to="/setting" class="setting">
        <img src="../../../static/images/me/setting.png" alt="">
      </router-link>
    </div>
    <div :style="{backgroundImage: 'url(' + bg + ')' }" class="surplus_money">
      <p class="money">
        账户余额：{{money}}元
      </p>
    </div>
    <div class="more_page">
      <!-- <ul>
        <router-link tag="li" :to="item.link" v-for="(item,index) in page1" :key="index">
          <i class="iconfont">
            <img :src="item.icon" alt="">
          </i>
          <p>{{item.title}}</p>
          <span class="iconfont">&#xe620;</span>
        </router-link>
      </ul> -->
      <!-- <div class="hx"></div> -->
      <ul>
        <router-link tag="li" :to="item.link" v-for="(item,index) in page2" :key="index">
          <i class="iconfont">
            <img :src="item.icon" alt="">
          </i>
          <p>{{item.title}}</p>
          <span class="iconfont">&#xe620;</span>
        </router-link>
      </ul>
      <div class="hx"></div>
      <ul>
        <router-link tag="li" :to="item.link" v-for="(item,index) in page3" :key="index">
          <i class="iconfont">
            <img :src="item.icon" alt="">
          </i>
          <p>{{item.title}}</p>
          <span class="iconfont">&#xe620;</span>
        </router-link>
      </ul>
    </div>    

  </div>
</template>

<script>
import bg2 from "@/../static/images/surplus_money.jpg";
import Axios from "../../common/js/getAxios";
export default {
  beforeCreate: function() {
        document.getElementsByTagName("body")[0].className="bg";
    },
    beforeDestroy: function() {
        document.body.removeAttribute("class","bg");
    },
  mounted() {
    this.getUserInfo();
  },
  methods: {
    login() {
      if (!this.isLogin) {
        this.$router.push("/loginRegistrat");
      }
    },
    getUserInfo() {
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosGet(
        "/api/user/",
        {},
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          if (res) {
            this.isLogin = true;
            this.money = res.data.data.money;
            this.username = res.data.data.name;
            console.log(res);
            if (res.data.data.image) {
              this.userimg = res.data.data.image;
            } else {
              this.userimg = "../../static/images/user_icon_big.png";
              console.log(2);
            }
          } else {
            this.$message.error("登录失败，请重新登录");
          }
        },
        err => {
          //  this.$message("请登录查看更多页面");
        }
      );
    }
  },
  data() {
    return {
      //判断是否已经登录
      isLogin: false,
      //用户余额
      money: "--",
      // 用户姓名
      username: "请登录",
      userimg: "@/../static/images/user_icon_big.png",
      bg: bg2,
      page1: [
        // {
        //   title : "我的推广",
        //   icon : "@/../static/images/me/mav_01.png",
        //   link : "/adv"
        // },
        // {
        //   title : "牛人收益",
        //   icon : "@/../static/images/me/mav_02.png",
        //   link : ""
        // },
        // {
        //   title : "我的关注",
        //   icon : "@/../static/images/me/mav_03.png",
        //   link : ""
        // }
      ],
      page2: [
        {
          title: "公告列表",
          icon: "@/../static/images/me/mav_02.png",
          link: "/outNotice"
        },
        {
          title: "资金明细",
          icon: "@/../static/images/me/mav_04.png",
          link: "/myMoney"
        },
        {
          title: "我的策略",
          icon: "@/../static/images/me/mav_05.png",
          link: "/cl"
        }
      ],
      page3: [
        {
          title: "关注牛人",
          icon: "@/../static/images/me/mav_03.png",
          link: "/followBull"
        },
        {
          title: "客服中心",
          icon: "@/../static/images/me/mav_01.png",
          link: "/kf"
        },
        {
          title: "实名认证",
          icon: "@/../static/images/me/mav_06.png",
          link: "/rz"
        },
        {
          title: "合作协议",
          icon: "@/../static/images/me/mav_07.png",
          link: "/agreeMent"
        }
      ]
    };
  }
};
</script>

<style lang="stylus" scoped>
@import '~@/assets/css/me/me';
</style>



