<template>
  <div class="div">
    <return-top>
      <p slot="title">新手指引</p>
    </return-top>
    <swiper :options="swiperOption" ref="mySwiper">
        <!-- slides -->
        <swiper-slide>
          <img src="@/../static/images/new_router/router_02.jpg" />
        </swiper-slide>
        <swiper-slide>
          <img src="@/../static/images/new_router/router_04.jpg" />
        </swiper-slide>
        <swiper-slide>
          <img src="@/../static/images/new_router/router_06.jpg" />
        </swiper-slide>
        <swiper-slide>
          <img src="@/../static/images/new_router/router_08.jpg" />
        </swiper-slide>
        <!-- Optional controls -->
        <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
  </div>
</template>

<script>
import ReturnTop from "@/components/ReturnTop";
export default {
  data(){
    return {
      swiperOption: {
        autoplay: true,
        loop: false,
        pagination: {
          // el: ".swiper-pagination"
        }
      }
    }
  },
  components : {
    ReturnTop
  }
}
</script>

<style lang="stylus" scoped>
.div{
  img{
    width 100%;
  }
}
</style>



