<template>
  <div>
    <return-top>
      <p slot="title">忘记密码</p>
    </return-top>

    <div class="order">
      <span class="qes">手机号码</span>
      <input type="text" v-model="tel" placeholder="请输入注册手机号码" class="a">
      <span class="send" @click="select()" style="border : none;">选择机构</span>
    </div>

    <div class="order">
      <span class="qes">验证码</span>
      <input v-model="msg" type="text" placeholder="请在输入验证码" class="a">
      <span @click="sendMsg()" class="send">{{getMsg}}</span>
    </div>

    <div class="order">
      <span class="qes">登录密码</span>
      <input v-model="password" type="text" placeholder="请输入6~12位密码" class="a">
    </div>
    <div class="order">
      <span class="qes">确认密码</span>
      <input v-model="passwordRepeat" type="text" placeholder="请在输入密码" class="a">
    </div>    

    <div @click="sendRegist()" class="submit">
      确认
    </div>

    <div class="model" v-show="modelShow">
      <div class="box">
        <div class="top">选择登录会员单位</div>
        <div class="bottom">
          <p @click="selectBesure()">测试专用</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ReturnTop from "@/components/ReturnTop";
import Axios from "../../common/js/getAxios";

export default {
  data() {
    return {
      //倒计时
      num : 60,
      send : true,
      //动态修改倒计时
      getMsg : "获取验证码",
      value3: true,
      modelShow : false,
      tel : "",
      passwordRepeat : "",
      msg : "",
      password : "",
      verificationKey : ""
    };
  },
  components: {
    ReturnTop
  },
  methods : {
    //发送验证码
    sendMsg(){
      if(!this.send){
         this.$message(`请在${this.num}秒后再次发送验证码`)
        return false;
      }
      Axios.postAxios('/api/verificationCode/forgetPassword',{
        phone : this.tel,        
      }).then((res)=>{        
        if(res.data.msg == "手机号已被注册！"){
           this.$message("手机号已被注册！");
        }else if(res.data.status){
           this.$message("短信发送成功");
          this.verificationKey = res.data.data.verificationKey
          this.send = false;
          //修改定时器this指向，也可使用箭头函数
          const _this = this;
          let time = setInterval(function(){
            _this.num--;
            _this.getMsg = _this.num;            
            // console.log(_this.num)
            if(_this.num == 0){
              _this.num = 60;
              clearInterval(time);
              _this.send = true;
              _this.getMsg = "获取验证码"
            }
          },1000)
        }else if(res.data.msg == "请输入手机号！"){
           this.$message("请输入手机号！");
        }else if(res.data.msg == "请输入正确的手机号！"){
           this.$message("请输入正确的手机号！");
        }else{
           this.$message("此手机号未注册");
        }
      },(err)=>{
        console.log(err.response);
      })
    },
    //请求重置密码
    sendRegist(){
      //非空验证
      if(false){        
      }else if(this.tel == ""){
         this.$message("手机号码不能为空");
        return false;
      }else if(this.msg == ""){
         this.$message("验证码不能为空");
        return false;
      }else if(this.password == ""){
         this.$message("密码不能为空");
        return false;
      }else if(this.passwordRepeat == ""){
         this.$message("确认密码不能为空");
        return false;
      }
      Axios.postAxios('/api/forgetPassword',{
        phone : this.tel,
        verificationKey : this.verificationKey,
        verificationCode : this.msg,
        password : this.password,
        passwordRepeat : this.passwordRepeat
      }).then((res)=>{  
        console.log(res);
        if(res.statusText != "OK"){
           this.$message("系统异常,请检查验证码是否正确以及密码与确认密码是否相同");
        }else{
           this.$message("注册成功,请重新登陆");
          this.$router.push("/loginRegistrat");
        }
      },(err)=>{
        console.log(err.response);
      })
    },
    
    select(){
      this.modelShow = true;
    },
    selectBesure(){
      this.modelShow = false;
    }
  }
};
</script>
<style lang="stylus" scoped>
@import '~@/assets/css/me/third';
.model{
  position: fixed
  top: 0
  bottom: 0
  left: 0
  right: 0
  background: rgba(0,0,0,.3)
  .box{
    border-radius 6px;
    width 6rem    
    position absolute
    height auto
    top 50%
    transform translateY(-50%)
    left 0
    right 0
    margin auto
    .top{
      height .8rem
      border-radius 6px 6px 0 0;
      background #fc5155
      color #fff
      line-height .8rem
      text-indent 2em
      width 6rem
    }
    .bottom{
      width 6rem
      background #FFF
      padding-bottom .2rem
      border-radius 0 0 6px 6px
      @extend .nobefore
      p{
        width 5.4rem
        height .82rem
        margin .2rem auto 0
        border 1px solid #eaeaea
        line-height .82rem
        text-indent 2em
        font-size .26rem
        color #303030
      }
    }
  }
}
.send {
  text-align: center;
  color: #fc5155;
  font-size: 0.24rem;
  padding: 0.08rem;
  position: absolute;
  right: 0.26rem;
  line-height: 0.54rem;
  top: 0.15rem;
  border: 1px solid #fc5155;
  border-radius: 4px;
  min-width 1.02rem;
}

.el-switch {
  position: absolute;
  right: 0.28rem;
  top: 0;
  bottom: 0;
  margin: auto;
}

.a {
  // text-align: right;
  line-height: 1rem !important;
  height: 1rem !important;

  img {
    width: 100%;
  }
}

.ts {
  position: absolute;
  left: 6.48rem !important;
  top: 0;
  bottom: 0;
  width: 0.6rem;
  height: 0.6rem;
  display: inline-block;
}

.order {
  line-height: 1rem;
}
</style>



