        <p class="item"><span>户名</span> <span class="rig">{{rnm.name}}</span></p>    
        <p class="item"><span>开户银行</span> <span class="rig">{{rnm.bank}}</span></p>    
        <p class="item"><span>开户行地址</span> <span class="rig">{{rnm.address}}</span></p>    
