<script>
import ReturnTop from "@/components/ReturnTop";
import Axios from "../../common/js/getAxios";

export default {
  beforeCreate: function() {
    document.getElementsByTagName("body")[0].className = "bg2";
  },
  beforeDestroy: function() {
    document.body.removeAttribute("class", "bg2");
  },
  mounted(){
    // this.getYhk();
  },
  data() {
    return {
      isShow: false,
      selectType: "企业支付宝 ",
      other: "企业对公账户",
      activeNames: ["0"],
      ewm: require("@/assets/ewm.jpg"),
      dg:"",
      sms: "",
      sms2: "",
      rnm:""
    };
  },
  methods: {
    pay(){
      let userToken = localStorage.getItem("Authorization");
      Axios.postAxios('/api/payGo',{
        money:this.sms
      },{
        Authorization: "Bearer " + userToken
      }).then(res=>{
        window.location.href=res.data.payurl
      })
    },
    getYhk() {
      Axios.axiosGet(
        "/api/recharge/",
        {},
        {
          // Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          this.dg = res.data.dg_img;
          this.ewm = res.data.zfb_img;           
          this.rnm = res.data
        },
        err => {
          this.$message("请检查网络或登录状态");
        }
      );
    },
    change() {
      console.log(1);
    },
    changeType() {
      let tmp = this.selectType;
      this.selectType = this.other;
      this.other = tmp;
      this.isShow = !this.isShow;
      this.activeNames = ["0"];
    }
  },
  components: {
    ReturnTop
  }
};
</script>
<style lang="stylus" scoped>
h1 {
  font-size: 0.4rem;
  color: #000;
  margin: 0.28rem auto;
  text-align: center;
}
.box {
  width: 6.6rem;
  box-shadow: rgb(165, 165, 165) 0px 0px 3px 0px;
  margin: 0 auto;
  overflow: hidden;
  border-radius: 10px;
  .show {
    padding: 0.3rem 0.6rem;
    background: #fff;
    .item {
      display: flex;
      justify-content: space-between;
      margin-bottom: 0.1rem;
      span {
        flex:1;
        width: 1.64rem;
        font-size:.3rem
      }
      .rig{
        text-align: right;

      }
    }
  }
}
</style>




