<template>
  <div>
    <return-top>
      <p slot="title">支付协议</p>
    </return-top>
    <div v-for="(item,index) in message" :key="index" class="box">
      <p class="title">{{item.title}}</p>
      <p class="txt" v-html="item.announcement">{{item.announcement}}</p>
    </div>
  </div>
</template>

<script>
import ReturnTop from "@/components/ReturnTop";

export default {
  components: {
    ReturnTop
  }
};
item.announcement
</script>
<script>
  import ReturnTop from "@/components/ReturnTop";
  import Axios from '../../common/js/getAxios'

  export default {
  data(){
  return {
  message : []
  }
  },
  mounted(){
  this.getAgree();
  },
  components: {
  ReturnTop
  },
  methods : {
  getAgree(){
  let userToken = localStorage.getItem("Authorization");
  Axios.axiosGet('/api/payagreement/',{},{
  "Authorization" : "Bearer "+userToken,
  }).then((res)=>{
  console.log(res.data.msg);
  this.message = this.message.concat(res.data.msg);
  },(err)=>{
   this.$message("请检查网络或登录状态");
  })
  }
  }
  }
</script>
<style lang="stylus" scoped>
.box {
  padding: 0 0.3rem;
}

.title {
  margin-top: 0.36rem;
  font-size: 0.32rem;
  color: #303030;
  font-weight: bold;
}

.container {
  font-size: 0.3rem;
  margin-top: 0.1rem;
  color: #8c8c8c;
}
</style>




