<template>
  <div>
    <div v-show="ojbkShow" class="zz"></div>
    <div v-show="ojbkShow" class="outer_box1">
      <div class="box">
        <span class="stop">止盈</span>
        <el-input type="number" v-model="stopIn" placeholder="请输入止盈价格"></el-input>
      </div>
      <div class="box">
        <span class="down_green stop">止损</span>
        <el-input type="number" v-model="stopOut" placeholder="请输入止损价格"></el-input>
      </div>
      <div class="box">
        <el-button @click="hide()" type="info">取消</el-button>
        <el-button @click="ojbk()" type="danger">确定</el-button>
      </div>
    </div>
    <div class="tab">
      <ul>
        <li @click="tabClick(index)" v-for="(item,index) in tab" :key="index" :class="{active:index == tabIndex}">{{item.title}}</li>
      </ul>
    </div>
    <div v-show="tabIndex == 1" class="tab1">
      <!-- 搜索框 -->
      <div class="search">
        <i><img src="../../../static/images/search.png" /></i>
        <input @blur.prevent="changeCount()" placeholder="请输入股票代码" v-model="searchInput" type="" name="" id="" />
      </div>
      <div class="search_div">
        <ul>
          <li @click="postSearch(item.code)" v-for="(item,index) in addInput" :key="index">
            <span class="left">{{item.code | filtersName}}</span>
            <span style="margin-left:.2rem; float:right;" class="left">{{item.name}}</span>
            <!-- <span @click="addMyself(item.code)" class="right">点击添加</span> -->
          </li>
        </ul>
      </div>
      <div class="title">
        <p class="sx"></p>
        <p class="left">搜索历史</p>
        <p @click="removeAll()" class="right">清空</p>
      </div>
      <div class="searh_history">
        <li v-for="(item,index) in history" :key="index" class="li">
          <router-link :to="{path : '/ks',query:{code:item[0]}}">
            <p class="name">{{item[1]}} {{item[0] | filtersName}}</p>
            <p :class="{down_green:(item[4]-item[3])/item[3] <0}" class="num">{{item[3]}}</p>
            <p :class="{down_green:(item[4]-item[3])/item[3] <0}" class="percentage">{{(item[4]-item[3])/item[3] | toPercentage}}</p>
          </router-link>
          <p @click="remove(item[0])" class="close iconfont">&#xe621;</p>
        </li>
        <div style="margin-top:.26rem;" class="title">
          <div style="width:3rem;margin:0 auto">
            <div @click="upDown(0)" class="mleft">&lt;</div>
            <div @click="upDown(1)" class="mright">&gt;</div>
          </div>
        </div>
        <div class="title">
          <p class="sx"></p>
          <p class="left">热门推荐</p>
        </div>
      </div>
      <div class="hot">
        <ul>
          <router-link tag="li" :to="{path : '/ks',query:{code:share[0]}}" :class="{nomargin:index == 2}" v-for="(share,index) in hotShares" :key="index">
            <p class="top">{{share[1]}}</p>
            <p class="center">{{share[0] | filtersName}}</p>
            <p :class="{down_green:(share[4]-share[3])/share[3]<0}" class="num">{{parseFloat(share[4]).toFixed(2)}}</p>
            <p :class="{down_green:(share[4]-share[3])/share[3]<0}" class="bottom">
              {{(share[4]-share[3]).toFixed(2)}}&nbsp;&nbsp;&nbsp;{{(share[4]-share[3])/share[3] | toPercentage}}
            </p>
          </router-link>
        </ul>
      </div>
    </div>

    <div v-show="tabIndex == 0" class="tab2">
      <div class="money">
        <ul>
          <li class="left">
            <p class="top">净资产</p>
            <p class="num">￥{{accountCount.asset}}</p>
          </li>
          <li class="right">
            <p class="top">可用资金</p>
            <p class="num">￥{{accountCount.can_use}}</p>
          </li>
        </ul>
        <ul>
          <li class="left">
            <p class="top">持仓市值</p>
            <p class="num">￥{{accountCount.market_value}}</p>
          </li>
          <li class="right">
            <p class="top">浮动盈亏</p>
            <p class="num">￥{{accountCount.float_earn}}</p>
          </li>
        </ul>
      </div>
      <div class="snav">
        <ul>
          <li @click="snavClick(index)" :class="{active:index == sindex}" class="txt" v-for="(item,index) in snav" :key="index">{{item.title}}</li>
          <li class="sx"></li>
          <li class="sx"></li>
        </ul>
      </div>
      <div class="snav1">
        <div v-show="sindex == 0" v-if="nowStatus != 0" v-for="(item,index) in nowCc" :key="'chi'+index" class="now">
          <div @click="isShow(index)" class="title_box">
            <div class="left">{{item.name}}</div>
            <div class="right">{{item.code}}</div>
          </div>
          <ul v-show="item.is_show == 1">
            <li>信用金 <span>{{item.xingyongjin}}</span></li>
            <li>买入价 <span>{{item.price}}</span></li>
            <li>止 盈 <span>{{item.max_price}}</span></li>
            <li>股票数 <span>{{item.num}}</span></li>
            <li>现 价 <span :class="{down_green:item.current_price-item.price<0}" style="color:#ef584d">{{item.current_price}}</span></li>
            <li>止 损 <span>{{item.min_price}}</span></li>
            <li>浮动盈亏 <span :class="{down_green:item.float_earn<0}" style="color:#ef584d">{{item.float_earn}}</span></li>
            <li style="width:60%">创建时间 <span>{{item.created_at}}</span></li>

            <div v-for="(item2,index) in item.xingyongjin_appends" :key="index" class="box">
                <li style="width:50%">追加信用金：<span>{{item2.money}}</span></li>
                <li style="width:50%">追加时间：<span>{{item2.created_at}}</span></li>
            </div>

            <li>市 值 <span>{{item.market_value}}</span></li>


            <div class="sell">
              <div class="box">
                <div class="left">当前递延状态：<span v-if="item.is_sell == 1">不递延</span><span v-if="item.is_sell == 2">递延</span></div>
              </div>
              <div class="box">
                <div class="left">当前:还剩下{{(item.current_price-item.min_price).toFixed(2)}}元即将到达止损价</div>
              </div>
              <div class="box">
                <div class="left">每天14：00扣除递延费，请保持可用余额充足</div>
              </div>

            </div>
            

            <div class="sell2">
              <div @click="sell(item.tactic_id)" class="button active">卖出</div>
              <div @click="stop(item.tactic_id,item.max_price,item.min_price,item.min_min_price)" class="button">止盈止损</div>              
              <div v-if="item.is_sell == 1" @click="delay(item.tactic_id,2)" class="button">递延</div>              
              <div v-if="item.is_sell == 2" @click="delay(item.tactic_id,1)" class="button nomargin">不递延</div>              
            </div>
            <div v-if="item.is_add == 1" class="sell2">               
              <input class="button" style="flex:.6;" v-model="xingyongjin" type="text"> 
              <div @click="addXyj(item.tactic_id,xingyongjin)" style="flex:.4;margin-left:.2rem;" class="button active">追加信用金</div>
            </div>
          </ul>
        </div>

<div v-show="sindex == 1" @click="isShow3(index)" v-for="(item,index) in wt" :key="index" class="now">
          <div class="title_box">
            <div class="left">{{item.name}}&nbsp;&nbsp;&nbsp;<span style="font-size:.3rem;">{{item.code}}</span></div>
            <div v-if="item.out_in == 0" class="right">买入</div>
            <div v-if="item.out_in == 2" class="right">卖出</div>
          </div>
          <ul v-show="item.is_show == 1">
            <li>买价 <span>{{item.price}}</span></li>
            <li>当前价 <span>{{item.current_price}}</span></li>
            <li>股票数 <span>{{item.num}}</span></li>
            <li>总信用金 <span>{{item.count_xingyongjin}}</span></li>
            <li>止盈 <span>{{item.max_price}}</span></li>
            <li>止损 <span>{{item.min_price}}</span></li>
            <li style="width:100%;">创建时间 <span>{{item.created_at}}</span></li>        
          </ul>
        </div>

        <div v-show="sindex == 2" @click="isShow2(index)" v-for="(item,index) in historyInfo" :key="'his_'+index" class="now">
          <div class="title_box">
            <div class="left">{{item.name}}</div>
            <div class="right">{{item.code}}</div>
          </div>
          <ul v-show="item.is_show == 1">
            <li>信用金 <span>{{item.xingyongjin}}</span></li>
            <li>买价 <span>{{item.price}}</span></li>
            <li>卖价 <span>{{item.deal_money}}</span></li>
            <li>股票数 <span>{{item.num}}</span></li>
            <li>盈亏 <span>{{item.profit}}</span></li>
            <li>收益率 <span>{{item.rate}}</span></li>
            <li>买入 <span>{{item.created_at}}</span></li>
            <li>卖出 <span>{{item.deal_time}}</span></li>
            <li>建仓费 <span>{{item.cangfei}}</span></li>
            <li>递延费 <span>{{item.count_diyanfei}}</span></li>
            <li style="width:100%">平仓类型 <span>{{item.pingcang_type}}</span></li>            
          </ul>
        </div>
        <p v-if="nowStatus == 0" class="nothing">暂无数据~</p>
      </div>
    </div>
  </div>
</template>

<script>
import { Dialog } from "vant";

import Axios from "../../common/js/getAxios";
export default {
  filters: {
    //转成百分数
    toPercentage(value) {
      let str = Number(value * 100).toFixed(2);
      str += "%";
      return str;
    }
  },
  data() {
    return {
      xingyongjin:1000,
      wt : [],
      time : "",
      //定时器      
      input: '',
      //热门股票
      hotShares : [],
      //历史交易信息
      historyInfo : [],
      //用户信息
      accountCount : {},
      tmpId : "",
      ojbkShow : false,
      stopIn : 0,
      stopOut : 0,
      //临时存储最小止损
      tmpOut : 0,
      // 历史记录页码数
      historyPage: 1,
      //历史记录
      history: [],
      //问题
      searchInput: [],
      //搜索显示框
      addInput: [],
      //当前持仓
      nowCc: [],
      //当前持仓是否为空
      nowStatus: 0,
      sindex: 0,
      tabIndex: 0,
      tab: [
        {
          title: "持仓"
        },
        // {
        //   title: "策略"
        // }
        
      ],
      snav: [
        {
          title: "当前持仓"
        },
        {
          title: "当前委托"
        },
        {
          title: "历史交易"
        }
      ]
    };
  },
  watch: {
    searchInput(v) {
      // this.searchCl(v);
      Axios.axiosGet("/api/stock/search", { code: v }).then(
        res => {
          //这里因为需要重新渲染页面，所以需要使用push，不能直接赋值
          this.addInput = res.data.data;          
        },
        err => {
          console.log(err);
        }
      );

    }
  },
  mounted() {
    this.getNow();
    this.showHistory();
    this.getAccountCount();
    this.getHot();
    // this.repeat();
    this.time = setInterval(()=>{
      // this.getNow();
      // this.getAccountCount();
    },3000)
  },
  //切换页面销毁定时器
  beforeDestroy(){
    clearInterval(this.time)
  },
  methods: {
    //获取委托
    getWt(){
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosGet(
        "/api/strategyEntrust",
        {
          // code: code
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {          
            console.log(res);          
          this.wt = res.data.data;    
        },
        err => {
          console.log(err);
        }
      );
    },
    // repeat(){
    //   this.time = setInterval(()=>{
    //     this.getNow();    
    //     this.getAccountCount();
    //   },1000)
    // },

    //添加到搜索的历史记录，并跳转到K线页面
    postSearch(code) {
      let userToken = localStorage.getItem("Authorization");
      Axios.postAxios(
        "/api/search",
        {
          code: code
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          if (res.status) {
            console.log("历史记录添加成功");
          }
          //跳转到对应的K线页面
          this.$router.push({ path: "/ks", query: { code: code } });
        },
        err => {
          console.log(err);
        }
      );
    },

    
    addXyj(id,xyj){
      let userToken = localStorage.getItem("Authorization");
      Axios.postAxios(
        '/api/tactics/addshoudong',
        {
          tactic_id : id,
          // xingyongjin : this.input
          xingyongjin : xyj
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(res=>{
          console.log(res.data)          
          if(res.data.status){            
            this.$message.success(res.data.msg);
          }else{            
            this.$message.error(res.data.msg);
          }
      },err=>{
        console.log(err);
        this.$message.error('添加失败');
      })
    },
    //获取热门推荐股票
    getHot(){
      Axios.axiosGet(
        "/api/goods/hot",
        {
        },
        {
        }
      ).then(
        res => {
          // console.log(res)
          this.hotShares = res.data.data;
        },
        err => {}
      );
    },
    //获取历史交易信息
    getHistoryInfo(){
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosGet(
        "/api/strategyHistory/normal",
        {
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          this.historyInfo = res.data.data;
          console.log(res)
        },
        err => {}
      );
    },
    //获取用户账户信息
    getAccountCount(){
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosGet(
        "/api/accountCount/normal",
        {
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          this.accountCount = res.data.data;
        },
        err => {}
      );
    },
    // 卖出
    async sell(id){
      let userToken = localStorage.getItem("Authorization");

      let ok = true;
      await Dialog.confirm({
        title: '确认卖出',
        message: '您确认卖出此策略吗'
      }).then(() => {
        // on confirm
      }).catch(() => {
        ok = false;
      });

      if(!ok){
        return false;
      }
      Axios.postAxios(
        '/api/tactics/sale',
        {
          tactic_id : id
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(res=>{
        if(res.data.status == false){          
           this.$message.error(res.data.msg);
        }else{
          this.$message.success(res.data.msg)
        this.getNow();
        }
      },err=>{
        console.log(err);
      })
    },
    stop(id,max,min,min_min_price){
      this.tmpId = id;
      this.ojbkShow = true;
      this.stopIn = max;
      this.stopOut = min;
      this.tmpOut = min_min_price;
    },
    hide(){
      this.tmpId = "";
      this.ojbkShow = false;
    },
    // 确认修改止盈止损
    // 确认修改止盈止损
    ojbk(){
      let userToken = localStorage.getItem("Authorization");
      if(this.stopOut < this.tmpOut){
        this.$message.error("最小止损不能小于"+this.tmpOut);        
        return false;
      }
      Axios.postAxios(
        '/api/tactics/editprice',
        {
          tactic_id : this.tmpId,          
          max_price : this.stopIn,
          min_price : this.stopOut
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(res=>{
        console.log(res);
        if(!res.data.status){
          this.$message.success(res.data.msg);          
          return false;          
        }else{
          this.$message.success(res.data.msg);          
          this.getNow();
          this.ojbkShow = false;
        }
      },err=>{
        console.log(err);
      })
    },
    //点击递延
    delay(id,v){
      let userToken = localStorage.getItem("Authorization");
      Axios.postAxios(
        '/api/tactics/issell',
        {
          tactic_id : id,
          is_sell : v
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(res=>{
        if(!res.data.status){
          this.$message.error("修改失败");          

          return false;
        }else{
          this.$message.success("修改成功");

          this.getNow();
        }        
      },err=>{
        console.log(err);
      })
    },
    //移除单条数据
    remove(code){
      var that = this;
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosDelete(
        '/api/search',
        {
          code : code
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(res=>{        
        that.showHistory();
      },err=>{
        that.showHistory();
      })
    },
    // 移除全部数据
    removeAll(){
      var that = this;
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosDelete(
        '/api/search',
        {
          type : 1
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(res=>{        
        that.showHistory();
      },err=>{
        that.showHistory();
      })
    },
    //上下翻页
    upDown(page) {
      if (page == 0) {
        if (this.historyPage == 1) {
          return false;
        }
        this.historyPage--;
      } else if (page == 1) {
        this.historyPage++;
      }
      console.log(this.historyPage);
      this.showHistory();
    },
    // 显示搜索历史
    showHistory() {
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosGet(
        "/api/search",
        {
          page: this.historyPage
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          if (!res.data.status) {
            //防止翻页溢出            
            this.history.splice(0, this.history.length);
            this.upDown(0);
            return false;
          }
          //清空当前页面
          this.history.splice(0, this.history.length);
          this.history = this.history.concat(this.history, res.data.data);
        },
        err => {}
      );
    },
    //失去焦点
    changeCount() {},
    //是否显示更多
    isShow(index) {
      if (this.nowCc[index].is_show == 0) {
        this.nowCc[index].is_show = 1;
      } else {
        this.nowCc[index].is_show = 1;
      }
    },
    isShow2(index) {
      this.historyInfo[index].is_show = 1;
      
    },
    isShow3(index) {
      this.wt[index].is_show = 1;
      
    },
    //获取当前持仓
    getNow() {
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosGet(
        "/api/strategyChiCang/normal",
        {},
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          console.log(res)
          this.nowCc = res.data.data;
          this.nowStatus = res.data.status;
        },
        err => {          
          this.$router.push("/me");
        }
      );
    },
    //持仓委托交易
    snavClick(index) {
      this.sindex = index;
      if(index == 2){
        this.getHistoryInfo();
      }
      if(index == 0){
        this.getNow();
      }
      if(index == 1){
        this.getWt();
      }
    },
    // 策略持仓
    tabClick(index) {
      this.tabIndex = index;
      if(index == 1 ){
        this.getNow();
      }
    }
  }
};
</script>

<style lang="stylus" scoped>
@import '~@/assets/css/cl/cl';
  .tab ul{
    width:.7rem
  }
  .tab2 .money{
    background:#fff;
    p{
      color:#585858 !important;
    }
  }
  .tab2 .money ul li .num{
    margin-top:0 !important;
  }
  .tab2 .money ul{
    padding-bottom:.1rem;
    
  }
  .now ul li{
  font-size:.32rem
  // text-align:center
}
.now ul{
  padding: 0.2rem 0.25rem;
}
</style>





