<template>
  <div>
    <return-top>
      <p slot="title">实名认证</p>
    </return-top>
    <!-- 草泥马的你个傻逼认证过了，点进来干什么 -->
    <div v-if="isRz" class="yrz">
      <div class="sbbox">
        <img style="width:100%" src="static/smrz/yrz.png" alt="">
      </div>
    </div>

    <div v-if="isWRz">
      <img style="width:100%" src="static/smrz/title.jpg" alt="">
      <div class="form">
        <div class="label">
          <span>真实姓名</span>
          <input v-model="name" placeholder="请输入您的真实姓名" type="text">
        </div>
        <div class="label">
          <span>身份证号</span>
          <input v-model="id" placeholder="请输入您的身份证号" type="text">
        </div>
        <div class="label">
          <span>银行卡号</span>
          <input v-model="card" placeholder="请输入您的真实姓名" type="text">
        </div>
        <div class="label">
          <span style="flex:2">开户行地址</span>
          <input v-model="addr" placeholder="开户行地址" type="text">
        </div>
      </div>
      <p class="upload">
        上传证件材料
      </p>
      <div class="sc">
        <el-upload :headers="imgheader" class="avatar-uploader" action="/api/uploads" :show-file-list="false" :on-success="onRead1">
          <img style="width:100%" :src="tp1" alt="">
        </el-upload>
        <el-upload style="margin-top:.4rem" :headers="imgheader" class="avatar-uploader" action="/api/uploads" :show-file-list="false" :on-success="onRead2" :before-upload="before">
          <img style="width:100%" :src="tp2" alt="">
        </el-upload>
      </div>
      <div style="margin-top:1rem" @click="submit" class="submit">提交</div>
    </div>

  </div>
</template>

<script>
import { Uploader } from "vant";
import ReturnTop from "@/components/ReturnTop";
import Axios from "../../common/js/getAxios";

export default {
  beforeCreate(){
  },
  beforeMount(){
    this.isSm();
  },
  mounted: function() {
    document.getElementsByTagName("body")[0].className = "bg4";    
  },
  beforeDestroy: function() {
    document.body.removeAttribute("class", "bg4");
    document.body.removeAttribute("class", "bg5");
  },
  components: {
    ReturnTop
  },
  data() {
    return {
      isWRz:false,
      isRz: false,
      is_upload1: false,
      img1: "",
      img2: "",
      name: "",
      id: "",
      tp1: require("@/../static/smrz/tp_1.jpg"),
      tp2: require("@/../static/smrz/tp_2.jpg"),
      return1: "",
      return2: "",
      imgheader: {
        Authorization: "Bearer " + localStorage.getItem("Authorization")
      },
      card: "",
      addr: "",
      
    };
  },
  methods: {
    //判断是否实名
    isSm() {
      let userToken = localStorage.getItem("Authorization");
      Axios.postAxios(
        "/api/query",
        {},
        {
          Authorization: "Bearer " + userToken
        }
      ).then(res => {
        if (res.data.status) {
          this.isRz = true;
        }else{
          this.isWRz = true ;          
          document.getElementsByTagName("body")[0].className = "bg5";              

        }
      });
    },
    submit() {
      let userToken = localStorage.getItem("Authorization");
      Axios.postAxios(
        "/api/real_user",
        {
          realname: this.name,
          id_num: this.id,
          id_card_on: this.return1,
          id_card_off: this.return2,
          card_number: this.card,
          card_address: this.addr
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(res => {
        console.log(res);
        if (!res.data.status) {
          this.$message.error(res.data.msg);
        } else {
          this.$message.success(res.data.msg);
          this.$router.push("/me");
        }
      });
    },
    before() {
      if (!this.is_upload1) {
        this.$message.error("请先上传第一张图片！！！");
        return false;
      }
    },
    onRead1(res, file) {
      this.tp1 = URL.createObjectURL(file.raw);
      if (res.status == 200) {
        this.return1 = res.msg;
        this.is_upload1 = true;
      } else {
        this.$message.error("上传失败");
      }
    },
    onRead2(res, file) {
      this.tp2 = URL.createObjectURL(file.raw);
      if (res.status == 200) {
        this.return2 = res.msg;
      } else {
        this.$message.error("上传失败");
      }
      console.log(res);
    }
  }
};
</script>

<style lang="stylus" scoped>
.form {
  width: 6.72rem;
  height: 4.3rem;
  background: #ffffff;
  margin: 0.7rem auto;
  padding: 0.4rem;
  position: relative;
  z-index: 2;
  border-radius: 10px;
  .label {
    height: 0.8rem;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #ccc;
    span {
      flex: 1;
      font-size: 0.3rem;
      color: #464647;
      text-indent: 1em;
    }
    input {
      font-size: 0.3rem;
      color: #cccccc;
      flex: 2.5;
      text-align: center;
    }
  }
}
.upload {
  font-size: 0.3rem;
  color: #464647;
  line-height: 1rem;
  position: relative;
  z-index: 3;
  margin-top: 0.8rem;
  padding-left: 0.46rem;
}
.sc {
  width: 6.7rem;
  margin: 0 auto;
}
.submit {
  font-size: 0.32rem;
  color: #fff;
  width: 5.8rem;
  height: 0.83rem;
  background: #eb635d;
  text-align: center;
  line-height: 0.76rem;
  margin: 0.56rem auto;
  border-radius: 10px;
}
.yrz {
  position: fixed;
  top: 0.88rem;
  bottom: 0;
  left: 0;
  right: 0;
  background: #f2f2f2;
  z-index: 4;
  .sbbox {
    transform: translateY(-0.88rem);
    width: 4.7rem;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    position: absolute;
    height: 5.2rem;
  }
}
</style>




