<template>
  <div>
    <return-top>
      <p slot="title">模拟点买</p>
    </return-top>
    <div class="tab1">
      <div class="search">
        <i><img src="../../../static/images/search.png" /></i>
        <input @blur.prevent="changeCount()" placeholder="请输入股票代码" v-model="searchInput" type="" name="" id="" />
      </div>
      <div class="search_div">
        <ul>
          <li @click="postSearch(item.code)" v-for="(item,index) in addInput" :key="index">
            <span class="left">{{item.code}}</span>
            <span style="margin-left:.2rem;float:right;" class="left">{{item.name}}</span>
            <!-- <span @click="addMyself(item.code)" class="right">点击添加</span> -->
          </li>
        </ul>
      </div>
    </div>
    <div v-show="ojbkShow" class="zz"></div>
    <div v-show="ojbkShow" class="outer_box1">
      <div class="box">
        <span class="stop">止盈</span>
        <el-input type="number" v-model="stopIn" placeholder="请输入止盈价格"></el-input>
      </div>
      <div class="box">
        <span class="down_green stop">止损</span>
        <el-input type="number" v-model="stopOut" placeholder="请输入止损价格"></el-input>
      </div>
      <div class="box">
        <el-button @click="hide()" type="info">取消</el-button>
        <el-button @click="ojbk()" type="danger">确定</el-button>
      </div>
    </div>

    <!-- //创建策略 -->
    <!-- <div class="createBuy">
      创建<br>策略
    </div> -->

    <div class="tab2">
      <div class="money">
        <ul>
          <li class="left">
            <p class="top">净资产</p>
            <p class="num">￥{{accountCount.asset}}</p>
          </li>
          <li class="right">
            <p class="top">可用资金</p>
            <p class="num">￥{{accountCount.can_use}}</p>
          </li>
        </ul>
        <ul>
          <li class="left">
            <p class="top">持仓市值</p>
            <p class="num">￥{{accountCount.market_value}}</p>
          </li>
          <li class="right">
            <p class="top">浮动盈亏</p>
            <p class="num">￥{{accountCount.float_earn}}</p>
          </li>
        </ul>
      </div>
      <div class="snav">
        <ul>
          <li @click="snavClick(index)" :class="{active:index == sindex}" class="txt" v-for="(item,index) in snav" :key="index">{{item.title}}</li>
          <li class="sx"></li>
          <li class="sx"></li>
        </ul>
      </div>
      <div class="snav1">
        <div v-show="sindex == 0" v-if="nowStatus != 0" v-for="(item,index) in nowCc" :key="index" class="now">
          <div @click="isShow(index)" class="title_box">
            <div class="left">{{item.name}}</div>
            <div class="right">{{item.code}}</div>
          </div>
          <ul v-show="item.is_show == 1">
            <li>信用金 <span>{{item.xingyongjin}}</span></li>
            <li>买入价 <span>{{item.price}}</span></li>
            <li>止 盈 <span>{{item.max_price}}</span></li>
            <li>股票数 <span>{{item.num}}</span></li>
            <li>现 价 <span>{{item.current_price}}</span></li>
            <li>止 损 <span>{{item.min_price}}</span></li>
            <li>浮动盈亏 <span>{{item.float_earn}}</span></li>
            <li>创建时间 <span>{{item.created_at}}</span></li>
            <li>市 值 <span>{{item.market_value}}</span></li>
            <div class="sell">
              <div class="box">
                <div class="left">当前递延状态：<span v-if="item.is_sell == 1">不递延</span><span v-if="item.is_sell == 2">递延</span></div>
              </div>
              <div class="box">
                <div class="left">当前:还剩下{{(item.price-item.min_price).toFixed(2)}}元即将到达止损价</div>
              </div>
              <div class="box">
                <div class="left">每天14：50扣除递延费，请保持可用余额充足</div>
              </div>
            </div>
            <div class="sell2">
              <div @click="sell(item.tactic_id)" class="button active">卖出</div>
              <div @click="stop(item.tactic_id,item.max_price,item.min_price)" class="button">止盈止损</div>
              <div @click="delay(item.tactic_id,2)" class="button">递延</div>
              <div @click="delay(item.tactic_id,1)" class="button nomargin">不递延</div>
            </div>
            <div v-if="item.is_add == 1" class="sell2">
              <div style="flex:.6;" class="button">追加{{item.xingyongjin}}元</div>
              <div @click="addXyj(item.tactic_id,item.xingyongjin)" style="flex:.4;margin-left:.2rem;" class="button active">追加信用金</div>
            </div>
          </ul>
        </div>

        <div v-show="sindex == 2" @click="isShow2(index)" v-for="(item,index) in historyInfo" :key="item.code" class="now">
          <div class="title_box">
            <div class="left">{{item.name}}</div>
            <div class="right">{{item.code}}</div>
          </div>
          <ul v-show="item.is_show == 1">
            <li>信用金 <span>{{item.xingyongjin}}</span></li>
            <li>买价 <span>{{item.price}}</span></li>
            <li>卖价 <span>{{item.deal_money}}</span></li>
            <li>股票数 <span>{{item.num}}</span></li>
            <li>盈亏 <span>{{(Number(item.deal_money * 100).toFixed(2) - Number(item.price * 100).toFixed(2)) * item.num / 100}}</span></li>
            <li>收益率 <span>{{item.rate}}</span></li>
            <li>买入 <span>{{item.created_at}}</span></li>
            <li>卖出 <span>{{item.deal_time}}</span></li>
            <li>建仓费 <span>{{item.cangfei}}</span></li>
            <li>递延费 <span>{{item.count_diyanfei}}</span></li>
            <li style="width:100%">平仓类型 <span>{{item.pingcang_type}}</span></li>         
          </ul>
        </div>
        <p v-if="nowStatus == 0" class="nothing">暂无数据~</p>
      </div>
    </div>
  </div>
</template>

<script>
import Axios from "../../common/js/getAxios";
import ReturnTop from "@/components/ReturnTop";

export default {
  components: {
    ReturnTop
  },
  filters: {
    //转成百分数
    toPercentage(value) {
      let str = Number(value * 100).toFixed(2);
      str += "%";
      return str;
    }
  },
  data() {
    return {
      input: "",
      accountCount: {},
      //历史交易信息
      historyInfo: [],
      //用户信息
      userInfo: {},
      tmpId: "",
      ojbkShow: false,
      stopIn: 0,
      stopOut: 0,
      //临时存储最小止损
      tmpOut: 0,
      // 历史记录页码数
      historyPage: 1,
      //历史记录
      history: [],
      //问题
      searchInput: [],
      //搜索显示框
      addInput: [],
      //当前持仓
      nowCc: [],
      //当前持仓是否为空
      nowStatus: 0,
      sindex: 0,
      tabIndex: 0,
      tab: [
        {
          title: "策略"
        },
        {
          title: "持仓"
        }
      ],
      snav: [
        {
          title: "当前持仓"
        },
        {
          title: "当前委托"
        },
        {
          title: "历史交易"
        }
      ]
    };
  },
  watch: {
    searchInput(v) {
      // this.searchCl(v);
      Axios.axiosGet("/api/stock/search", { code: v }).then(
        res => {
          //这里因为需要重新渲染页面，所以需要使用push，不能直接赋值
          this.addInput = res.data.data;
        },
        err => {
          console.log(err);
        }
      );
    }
  },
  mounted() {
    this.getNow();
    this.getAccountCount();
  },
  methods: {
    //添加到搜索的历史记录，并跳转到K线页面
    postSearch(code) {
      let userToken = localStorage.getItem("Authorization");
      Axios.postAxios(
        "/api/search",
        {
          code: code
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          if (res.status) {
            console.log("历史记录添加成功");
          }
          //跳转到对应的K线页面
          this.$router.push({ path: "/ks", query: { code: code, isSim: 1 } });
        },
        err => {
          console.log(err);
        }
      );
    },
    addXyj(id, xyj) {
      let userToken = localStorage.getItem("Authorization");
      Axios.postAxios(
        "/api/tactics/addshoudong",
        {
          tactic_id: id,
          // xingyongjin : this.input
          xingyongjin: xyj
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          console.log(res);
          this.$message("添加成功");
        },
        err => {
          console.log(err);
          this.$message("添加失败");
        }
      );
    },

    //获取历史交易信息
    getHistoryInfo() {
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosGet(
        "/api/strategyHistory/analog",
        {},
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          this.historyInfo = res.data.data;
          console.log(res.data.data);
        },
        err => {}
      );
    },
    //获取用户账户信息
    getAccountCount() {
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosGet(
        "/api/accountCount/analog",
        {},
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          this.accountCount = res.data.data;
        },
        err => {}
      );
    },
    // 卖出
    sell(id) {
      let userToken = localStorage.getItem("Authorization");
      Axios.postAxios(
        "/api/tactics/sale",
        {
          tactic_id: id
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          console.log(res);
          this.$message.success(res.data.msg);
          this.getNow();
        },
        err => {
          console.log(err);
          this.$message.success(err.data.msg);
        }
      );
    },
    stop(id, max, min) {
      this.tmpId = id;
      this.ojbkShow = true;
      this.stopIn = max;
      this.stopOut = min;
      this.tmpOut = this.stopOut;
    },
    hide() {
      this.tmpId = "";
      this.ojbkShow = false;
    },
    // 确认修改止盈止损
    ojbk() {
      let userToken = localStorage.getItem("Authorization");
      if (this.stopOut < this.tmpOut) {
        this.$message("最小止损不能小于" + this.tmpOut);
        return false;
      }
      Axios.postAxios(
        "/api/tactics/editprice",
        {
          tactic_id: this.tmpId,
          max_price: this.stopIn,
          min_price: this.stopOut
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          console.log(res);
          if (!res.data.status) {
            this.$message("当前价格设置过低");
            return false;
          } else {
            this.$message("修改成功");
            this.getNow();
            this.ojbkShow = false;
          }
        },
        err => {
          console.log(err);
        }
      );
    },
    //点击递延
    delay(id, v) {
      let userToken = localStorage.getItem("Authorization");
      Axios.postAxios(
        "/api/tactics/issell",
        {
          tactic_id: id,
          is_sell: v
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          if (!res.data.status) {
            this.$message("修改失败");
            return false;
          } else {
            this.$message("修改成功");
            this.getNow();
          }
        },
        err => {
          console.log(err);
        }
      );
    },
    //移除单条数据
    remove(code) {
      var that = this;
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosDelete(
        "/api/search",
        {
          code: code
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          that.showHistory();
        },
        err => {
          that.showHistory();
        }
      );
    },
    // 移除全部数据
    removeAll() {
      var that = this;
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosDelete(
        "/api/search",
        {
          type: 1
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          that.showHistory();
        },
        err => {
          that.showHistory();
        }
      );
    },
    //上下翻页
    upDown(page) {
      if (page == 0) {
        if (this.historyPage == 1) {
          return false;
        }
        this.historyPage--;
      } else if (page == 1) {
        this.historyPage++;
      }
      console.log(this.historyPage);
      this.showHistory();
    },
    //失去焦点
    changeCount() {},
    //是否显示更多
    isShow(index) {
      if (this.nowCc[index].is_show == 0) {
        this.nowCc[index].is_show = 1;
      } else {
        this.nowCc[index].is_show = 0;
      }
    },
    isShow2(index) {
      console.log(index);
      if (this.historyInfo[index].is_show == 0) {
        this.historyInfo[index].is_show = 1;
      } else {
        this.historyInfo[index].is_show = 0;
      }
    },
    //获取当前持仓
    getNow() {
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosGet(
        "/api/strategyChiCang/analog",
        {},
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          console.log(res);
          this.nowCc = res.data.data;
          this.nowStatus = res.data.status;
        },
        err => {
          //  this.$message("页面获取失败，请刷新重试");
          this.$router.push("/me");
        }
      );
    },
    //持仓委托交易
    snavClick(index) {
      console.log(index);
      this.sindex = index;
      if (index == 2) {
        this.getHistoryInfo();
      } else if (index == 0) {
        this.getNow();
      }
    },
    // 策略持仓
    tabClick(index) {
      this.tabIndex = index;
      if (index == 1) {
        this.getNow();
      }
    }
  }
};
</script>

<style lang="stylus" scoped>
@import '~@/assets/css/cl/cl';
.createBuy {
  width: 50px;
  height: 50px;
  border-radius: 100%;
  background: #60bbfc;
  color: #fff;
  font-size: 14px;
  padding: 5px;
  text-align: center;
  position: absolute;
  right: 20px;
  bottom: 15px;
}
</style>





