<template>
  <div>
    <return-top>
      <p slot="title">最新动态</p>
    </return-top>
    <div class="tab2">

      <div class="center_box">
        <ul class="center">
          <li
            v-for="(item,index) in shareinfo"
            :key="index"
            class="item"
          >
            <div class="top">
              <p class="left">{{item.name}}</p>
              <router-link tag="p" :to="{path:'bullCreate',query:{id:item.id}}" class="right">跟买</router-link>
              <!-- <p class="right">跟买</p> -->
            </div>
            <div class="bottom">
              <ul>
                <li class="left down_green">卖出: <span>{{item.num}} 股</span></li>
                <li class="right">价格：<span>{{item.price}} 元</span></li>
                <!-- <li class="left">单笔收益：<span class="up_red">30.03%</span></li> -->
                <li class="right" style="width:100%">操作时间：<span>{{item.created_at}}</span></li>
              </ul>
            </div>
          </li>
        </ul>
      </div>

    </div>
  </div>
</template>

<script>
import ReturnTop from "@/components/ReturnTop";
import Axios from "../../common/js/getAxios";
import BScroll from "better-scroll";

export default {
  mounted() {
    this.getInit();
  },
  data() {
    return {
      page: 0,
      Cl: [],
      id: this.$route.query.id,
      shareinfo : []
    };
  },
  components: {
    ReturnTop
  },
  methods: {
    getInit() {
      Axios.axiosGet(
        "api/morebestmaninfo",
        {
          to_user_id: this.id
        },
        {}
      ).then(
        res => {
          this.shareinfo = res.data.data.data;
        },
        err => {
          console.log(err);
        }
      );
    }
  }
};
</script>

<style lang="stylus" scoped>
@import '~@/assets/css/cl/more2.styl';
@import '~@/assets/css/home/bullUser';
.center_box {
  padding-left: 0 !important;
  padding-top: 0;
}
</style>




