<template>
    <div>
        <return-top>
          <p slot="title">修改密码</p>
        </return-top>

        <!-- <div class="order">
            <span class="qes">旧密码</span>
            <input type="text" placeholder="请输入旧密码" class="a"> 
        </div> -->

        <div class="order">
            <span class="qes">新密码</span>
            <input type="number" v-model="pwd1" placeholder="请输入6~12位新密码" class="a"> 
        </div>

        <div class="order">
            <span class="qes">确认密码</span>
            <input type="number" v-model="pwd2" placeholder="请确认6~12位新密码" class="a"> 
        </div>

        <div @click="changePwd()" class="submit">
            提交
        </div>
    </div>
</template>

<script>
import ReturnTop from "@/components/ReturnTop";
import Axios from '../../common/js/getAxios'
export default {
    data(){
        return {
            pwd1 : "",
            pwd2 : "",
            userToken : ""
        }
    },
    mounted(){
        this.userToken = localStorage.getItem("Authorization");
    },
    components: {
        ReturnTop
    },
    methods : {
        changePwd(){
            if(this.pwd1){
                if(this.pwd1.length<6 || this.pwd1.length>12){
                     this.$message("密码长度必须在6-12位之间");
                    return false;
                }
                if(this.pwd1!=this.pwd2){
                     this.$message("新密码与确认密码必须相同");
                    return false;
                }
                Axios.postAxios("/api/changeuserpwd",{
                    password : this.pwd1
                },{
                    "Authorization" : "Bearer "+this.userToken
                }).then(res=>{
                    localStorage.clear();
                     this.$message("修改成功,请重新登录");
                    this.$router.push("/");
                },err=>{
                     this.$message("修改失败，请检查网络或登录状态")
                })
            }else{
                 this.$message("修改密码不能为空")
            }
        }
    }
};
</script>
<style lang="stylus" scoped>

@import '~@/assets/css/me/third';
.el-switch{
    position absolute
    right .28rem
    top 0
    bottom 0
    margin auto
}
.a {
    text-align: right;
    img{
        width 100%
    }
}
.ts{
    position absolute
    left 6.48rem !important
    top 0
    bottom 0
    width .6rem
    height .6rem
    display inline-block
}
</style>



