import axios from 'axios'
import qs from 'qs'
import { resolve, reject, promise } from 'when';
export default {
  postAxios(murl,mdata,mheader){
    return new promise((resolve,reject)=>{      
      axios({
        url: murl,
        method: 'post',
        data: qs.stringify(mdata),
        headers: mheader||{'Content-Type': 'application/x-www-form-urlencoded'}
      }).then((res)=>{
        resolve(res)
      }).catch((err)=>{
        reject(err);
      })
    })
  },
  axiosGet(murl,mdata,mheader){
    return new promise((resolve,reject)=>{
      axios.get(murl,{headers : mheader || {},params:mdata}).then((res)=>{
          resolve(res);
      }).catch((err)=>{
          reject(err);
      })
    })
  },
  axiosDelete(murl,mdata,mheader){
    return new Promise((resolve,reject)=>{
      axios.delete(murl,{headers : mheader || {},params:mdata}).then((res)=>{
        resolve(res);
      }).catch(err=>{
        reject(err);
      })
    })
  },
  axiosPut(murl,mdata,mheader){
    return new Promise((resolve,reject)=>{
      axios.put(murl,{headers : mheader || {},params:mdata}).then((res)=>{
        resolve(res);
      }).catch(err=>{
        reject(err);
      })
    })
  }
}


