export default {
  drawPie(myChart) {
    let data = [
      {
        name: "散户流入",
        value: "12"
      },
      {
        name: "散户流出",
        value: "13"
      },
      {
        name: "主力流入",
        value: "46"
      },
      {
        name: "主力流出",
        value: "30"
      }
    ]

    let option = {
      series: [
        {
          name: '姓名',          
          type: 'pie',
          radius: '55%',
          center: ['40%', '50%'],
          data: data,
          label: {
            formatter: '{b}: \n{d}',
            align : 'center'
          },
          itemStyle: {
            emphasis: {
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: 'rgba(0, 0, 0, 0.5)'
            }
          }
        }
      ]
    };

    myChart.setOption(option)
  }
}


