<template>
  <div>
    <return-top>
      <p slot="title">策略消息</p>
    </return-top>
    <router-link :to="{path : '/inNotice',query:{id:item.id}}" v-for="(item,index) in message" :key="index" tag="div" class="box">
      <div class="top">
        <p class="left">{{item.title}}</p>
        <p class="right">{{item.updated_at}}</p>
      </div>
      <p v-html="item.announcement" class="bottom"></p>
    </router-link>
  </div>
</template>

<script>
import ReturnTop from "@/components/ReturnTop";
import Axios from "../../common/js/getAxios";

export default {
  data() {
    return {
      message: []
    };
  },
  mounted() {
    this.getAgree();
  },
  components: {
    ReturnTop
  },
  methods: {
    getAgree() {
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosGet(
        "/api/park/",
        {},
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {    
            console.log(res)      
          this.message = this.message.concat(res.data.msg);
        },
        err => {
           this.$message("请检查网络或登录状态");
        }
      );
    }
  }
};
</script>
<style lang="stylus" scoped>
.box {
  padding: 0 0.3rem;
  border-bottom: 1px solid #eaeaea;
  padding-bottom: 0.4rem;
  &:before {
    content: '';
    display: table;
  }
  .top {
    display: flex;
    justify-content: space-between;
    margin: 10px 0 5px;
    .left {
      font-weight: 100;
      max-width : 220px;
      overflow : hidden;      
      font-size: 0.32rem;      
      white-space: nowrap;
      text-overflow:ellipsis
    }
    
  }
  .bottom {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 3;
        overflow: hidden;
        max-height 36px;
    }
}
</style>





