<template>
  <div>
    <return-top>
      <p slot="title">客服中心</p>
    </return-top>
    <div class="box">
      <p class="title">客服QQ：{{message.qq}}</p>
      <p class="title">客服邮箱：<a :href="'tel:'+message.tel">{{message.tel}}</a></p>
    </div> 
  </div>
</template>

<script>
import ReturnTop from "@/components/ReturnTop";
import Axios from "../../common/js/getAxios";

export default {
  data() {
    return {
      message: {}
    };
  },
  mounted() {
    this.getAgree();
  },
  components: {
    ReturnTop
  },
  methods: {
    getAgree() {
    //   let userToken = localStorage.getItem("Authorization");
      Axios.axiosGet(
        "/api/contactWay/",
        {},
        {
        //   Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          console.log(res);
          this.message = res.data.data
        },
        err => {
          this.$message("请检查网络或登录状态");
        }
      );
    }
  }
};
</script>
<style lang="stylus" scoped>
.box {
  padding: 0 0.3rem;
  &:before {
    content: '';
    display: table;
  }
  .title {
    margin-top: 0.36rem;
    font-size: 0.3rem;
    color: #303030;
    font-weight: bold;
  }
  .txt {
    font-size: 0.3rem;
    margin-top: 0.1rem;
    color: #303030;
  }
}
</style>





