<template>
    <div>
        <return-top>
            <p slot="title">资金明细</p>
        </return-top>

        <div class="top_box">
            <div class="top">
                <p class="name">当前余额（元）</p>
                <p class="num">{{money}}</p>
            </div>
            <div class="bottom">
                <router-link class="left" to="/charge">提现</router-link>
                <router-link class="right" to="/recharge">充值</router-link>
            </div>
        </div>

        <div class="tab">
            <div class="table">
                <ul>
                    <li @click="tabClick(index)" v-for="(item,index) in tab" :key="index" :class="{active:index == tabIndex}">{{item.title}}</li>
                </ul>
            </div>
        </div>

        <div v-show="tabIndex == 0" class="tab1">
            <ul>
                <li v-for="(item,index) in allMoney" :key="index">
                    <p  class="title">股票代码：{{item.code}}</p>
                    <div  class="more">
                        <p class="author">建仓费：{{item.cangfei}}</p>
                        <p style="margin-left:.4rem" class="date">递延费：{{item.diyanfei}}</p>
                        <p class="date">信用金：{{item.xingyongjin}}</p>
                    </div>
                </li>
            </ul>
            <div style="margin-top:.26rem;height:1rem;" class="title">
                <div style="width:3rem;margin:0 auto;">
                    <div @click="upDown(0,0)" class="mleft">&lt;</div>
                    <div @click="upDown(0,1)" class="mright">&gt;</div>
                </div>
            </div>
            <p v-if="allMoney.length == 0" class="nothing">暂无数据~</p>
        </div>

        <div v-show="tabIndex == 1" class="tab1">
            <ul>
                <li v-for="(item,index) in bzjMoney" :key="index">
                    <p  class="title">股票代码：{{item.code}}</p>
                    <div  class="more">
                        <p class="author">保证金：{{item.xingyongjin}}</p>
                    </div>
                </li>
            </ul>
            <div style="margin-top:.26rem;height:1rem;" class="title">
                <div style="width:3rem;margin:0 auto;">
                    <div @click="upDown(1,0)" class="mleft">&lt;</div>
                    <div @click="upDown(1,1)" class="mright">&gt;</div>
                </div>
            </div>
            <p v-if="allMoney.length == 0" class="nothing">暂无数据~</p>
        </div>

        <div v-show="tabIndex == 2" class="tab1">
            <ul>
                <li v-for="(item,index) in jcfMoney" :key="index">
                    <p  class="title">股票代码：{{item.code}}</p>
                    <div  class="more">
                        <p class="author">建仓费：{{item.cangfei}}</p>
                    </div>
                </li>
            </ul>
            <div style="margin-top:.26rem;height:1rem;" class="title">
                <div style="width:3rem;margin:0 auto;">
                    <div @click="upDown(2,0)" class="mleft">&lt;</div>
                    <div @click="upDown(2,1)" class="mright">&gt;</div>
                </div>
            </div>
            <p v-if="allMoney.length == 0" class="nothing">暂无数据~</p>
        </div>

        <div v-show="tabIndex == 3" class="tab1">
            <ul>
                <li v-for="(item,index) in dyfMoney" :key="index">
                    <p  class="title">股票代码：{{item.code}}</p>
                    <div  class="more">
                        <p class="author">递延费：{{item.diyanfei}}</p>
                    </div>
                </li>
            </ul>
            <div style="margin-top:.26rem;height:1rem;" class="title">
                <div style="width:3rem;margin:0 auto;">
                    <div @click="upDown(3,0)" class="mleft">&lt;</div>
                    <div @click="upDown(3,1)" class="mright">&gt;</div>
                </div>
            </div>
            <p v-if="allMoney.length == 0" class="nothing">暂无数据~</p>
        </div>

        <div v-show="tabIndex == 4" class="tab1">
            <ul>
                <li v-for="(item,index) in tx" :key="index">
                    <p  class="title">费用：{{item.money}}</p>
                    <div  class="more">
                        <p v-if="item.status == 1" class="author">审核状态：审核通过</p>
                        <p v-if="item.status == 2" class="author">审核状态：审核失败</p>
                        <p v-if="item.status == 0" class="author">审核状态：未审核</p>
                    </div>
                </li>
            </ul>
            <div style="margin-top:.26rem;height:1rem;" class="title">
                <div style="width:3rem;margin:0 auto;">
                    <div @click="upDown(4,0)" class="mleft">&lt;</div>
                    <div @click="upDown(4,1)" class="mright">&gt;</div>
                </div>
            </div>
            <p v-if="allMoney.length == 0" class="nothing">暂无数据~</p>
        </div>

    </div>
</template>

<script>
import Axios from "../../common/js/getAxios";
import ReturnTop from "@/components/ReturnTop";

export default {
  mounted() {
  
    this.getAll();
    this.getUserInfo();
  },
  data() {
    return {
      money:0,
      bzjMoney: [],
      bzjMoneyPage: 0,
      jcfMoney: [],
      jcfMoneyPage: [],
      dyfMoney: [],
      dyfMoneyPage: 0,
      tx:[],
      txPage:0,
      //全部的资金明细
      allMoney: [],
      allMoneyPage: 0,
      userToken: localStorage.getItem("Authorization"),
      tabIndex: 0,
      tab: [
        {
          title: "全部"
        },
        {
          title: "保证金"
        },
        {
          title: "建仓费"
        },
        {
          title: "递延费"
        },
        {
          title:"提现"
        }
      ]
    };
  },
  components: {
    ReturnTop
  },
  methods: {
    //控制上下翻页
    upDown(type, value) {
      switch (type) {
        case 0:
          if (value == 0) {
            if (this.allMoneyPage == 0) {
              return false;
            } else {
              this.allMoneyPage--;
            }
          } else {
            this.allMoneyPage++;
          }
          this.getAll();
          break;
        case 1:        
          if (value == 0) {
            if (this.bzjMoneyPage == 0) {
              return false;
            } else {
              this.bzjMoneyPage--;
            }
          } else {
            this.bzjMoneyPage++;
          }
          this.getBZJ();
          console.log(this.bzjMoneyPage)
          break;
        case 2:        
          if (value == 0) {
            if (this.jcfMoneyPage == 0) {
              return false;
            } else {
              this.jcfMoneyPage--;
            }
          } else {
            this.jcfMoneyPage++;
          }
          this.getJCF();
          console.log(this.jcfMoneyPage)
          break;
        case 3:        
          if (value == 0) {
            if (this.dyfMoneyPage == 0) {
              return false;
            } else {
              this.dyfMoneyPage--;
            }
          } else {
            this.dyfMoneyPage++;
          }
          this.getDYF();
          console.log(this.dyfMoneyPage)
          break;
        case 4:        
          if (value == 0) {
            if (this.txPage == 0) {
              return false;
            } else {
              this.txPage--;
            }
          } else {
            this.txPage++;
          }
          this.getTx();
          break;
      }
    },
    getUserInfo() {
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosGet(
        "/api/user/",
        {},
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          if (res) {
            console.log(res)
            this.money = res.data.data.money;
          }
        },
        err => {
          //  this.$message("请登录查看更多页面");
        }
      );
    },
    //获取全部资金
    getAll() {
      Axios.axiosGet(
        "/api/financialdetails",
        {
          page: this.allMoneyPage
        },
        {
          Authorization: "Bearer " + this.userToken
        }
      ).then(
        res => {
          this.allMoney.splice(0, this.allMoney.length);
          this.allMoney = this.allMoney.concat(res.data.data.data);
        },
        err => {
           this.$message("请登录后查看");
        }
      );
    },
    //获取保证金
    getBZJ() {
      Axios.axiosGet(
        "/api/financialdetails",
        {
          type: "bailaccount",
          page: this.bzjMoneyPage
        },
        {
          Authorization: "Bearer " + this.userToken
        }
      ).then(
        res => {
            
          this.bzjMoney.splice(0, this.bzjMoney.length);
          this.bzjMoney = this.bzjMoney.concat(res.data.data.data);
        },
        err => {
           this.$message("请登录后查看");
        }
      );
    },
    //获取提现
    getTx() {
      Axios.axiosGet(
        "/api/financialdetails",
        {
          type: "tixiancount",
          // page: this.bzjMoneyPage
        },
        {
          Authorization: "Bearer " + this.userToken
        }
      ).then(
        res => {
          this.tx.splice(0, this.tx.length);
          this.tx = res.data.data.data;         
        },
        err => {
           this.$message("请登录后查看");
        }
      );
    },
    //获取建仓费
    getJCF() {
      Axios.axiosGet(
        "/api/financialdetails?type=jiancangaccount",
        {
          page: this.jcfMoneyPage
        },
        {
          Authorization: "Bearer " + this.userToken
        }
      ).then(
        res => {            
          this.jcfMoney.splice(0, this.jcfMoney.length);
          this.jcfMoney = this.jcfMoney.concat(res.data.data.data);
        },
        err => {
           this.$message("请登录后查看");
        }
      );
    },
    //获取递延费
    getDYF() {
      Axios.axiosGet(
        "/api/financialdetails?type=diyanaccount",
        {
          page: this.dyfMoneyPage
        },
        {
          Authorization: "Bearer " + this.userToken
        }
      ).then(
        res => {            
          this.dyfMoney.splice(0, this.dyfMoney.length);
          this.dyfMoney = this.dyfMoney.concat(res.data.data.data);
        },
        err => {
           this.$message("请登录后查看");
        }
      );
    },
    tabClick(index) {
      this.tabIndex = index;
      if (index == 1) {
        this.getBZJ();
      }else if(index == 2){
        this.getJCF();
      }else if(index == 3){
        this.getDYF();
      }else if(index == 0){
        this.getAll();
      }else if(index == 4){
        this.getTx();
      }
    }
  }
};
</script>
<style lang="stylus" scoped>
@import '~@/common/css/variable';

.top_box {
    width: 6.9rem;
    height: 3.2rem;
    background: #ffffff;
    box-shadow: 0px 0px 16px #dadada;
    margin: 0.3rem auto;
    border-radius: 10px;

    .top {
        @extend .nobefore;
        height: 2.2rem;
        border-bottom: 1px solid #eaeae9;

        .name {
            margin-top: 0.56rem;
            font-size: 0.26rem;
            color: #909090;
            text-align: center;
        }

        .num {
            font-size: 0.56rem;
            color: #303030;
            font-weight: bold;
            text-align: center;
        }
    }

    .bottom {
        line-height: 1rem;
        font-size: 0.36rem;
        color: #575757;
        text-align: center;

        .left {
            float: left;
            border-right: 1px solid #eaeae9;
            width: 50%;
        }

        .right {
            width: 50%;
            float: left;
            color: #f6341a;
        }
    }
}

.table {
    width: 6.42rem;
    margin: 0 auto;
}

.active {
    color: #ef584d;

    &:after {
        content: '';
        width: 0.6rem;
        height: 0.04rem;
        background: #ef584d;
        border-radius: 2px;
        display: block;
        margin: 0.08rem auto 0;
    }
}

.tab {
    height: 0.9rem;
    box-shadow: 0px 0px 16px #dadada;
    @extend .noafter;

    ul {
        @extend .noafter;
        display: table-cell;
        vertical-align: middle;
        margin: 0 auto;
        width: 6.4rem;
        height: 0.9rem;

        li {
            float: left;
            font-size: 0.28rem;
            line-height: 0.4rem;
            width: 1.25rem;
            text-align: center;
            border-right: 1px solid #d5d5d4;
        }

        li:nth-child(5) {
            border: none;
        }
    }
}

.nothing {
    color: #707070;
    font-size: 0.26rem;
    text-align: center;
    margin-top: 0.34rem;
}

.tab1 {
    padding: 0 0.28rem;
}

.tab1 li {
    padding-top: 0.18rem;
    border-bottom: 1px solid #eaeae9;
}

.tab1 li .title {
    font-size: 0.3rem;
    color: #303030;
}

.tab1 li .more {
    font-size: 0.24rem;
    color: #909090;
    margin: 0.24rem auto;
    @extend .noafter;
}

.tab1 li .more .author {
    float: left;
}

.tab1 li .more .date {
    float: right;
}

.mleft, .mright {
    width: 0.6rem;
    line-height: 0.6rem;
    text-align: center;
    font-size: 0.4rem;
    font-weight: bold;
    border: 1px solid #d8d8d8;
    color: #aeaeae;
    border-radius: 6px;
}

.mleft {
    float: left;
}

.mright {
    float: right;
}
</style>



