
<template>
    <div>
        <return-top>
          <p slot="title">修改个人信息</p>
        </return-top>
        <div class="order">
            <span class="qes">头像</span>
            <span class="a ts">
                <img :src="imageUrl" alt="">
            </span>
            
            <div class="upload">
                <el-upload
            class="avatar-uploader"
            action="/api/avatar"
            :headers = "imgheader"
            :data = "imgdata"
            :show-file-list="false"
            :on-success="handleAvatarSuccess"
            :before-upload="beforeAvatarUpload">
                <img v-if="imageUrl" :src="imageUrl" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
            </div>

        </div>

        <div class="order">
            <span class="qes">昵称</span>
            <input type="text" placeholder="请输入需要修改的昵称" v-model="username" class="a b"> 
            <span @click="changeName()" data-v-3a803749="" class="send">确认修改</span>
        </div>

        <!--<div class="order">
            <span class="qes">手机号码</span>
            <input type="text" v-model="userphone" placeholder="请输入需要修改的手机号码" class="a b"> 
            <span @click="changePhone()" data-v-3a803749="" class="send">确认修改</span>
        </div>-->

        <div class="order">
            <span class="qes">登录密码</span>
            <router-link tag="span" to="/setting/modify" type="text" class="a">修改密码 </router-link>
        </div>


    

        <!-- <div class="order">
            <span class="qes">是否公开实盘信息</span>
            <el-switch v-model="value3">
            </el-switch>
        </div> -->


        <div @click="exit()" class="submit">
            退出系统
        </div>
    </div>
</template>

<style>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>

<script>
import ReturnTop from "@/components/ReturnTop";
import Axios from '../../common/js/getAxios'

export default {
    data(){
        return {
            //用户姓名
            username : "",
            //用户电话
            userphone : "",
            //用户token信息
            userToken : "",
            value3 : true,
            imageUrl: '@/../static/images/user.jpg',
            imgheader : {
                "Authorization" : "Bearer "+localStorage.getItem("Authorization")
            },
            file : "",
            imgdata : {
                name : this.file
            }
        }
    },
    mounted(){
        this.userToken = localStorage.getItem("Authorization");
    },
    components: {
        ReturnTop
    },
    methods : {
        //退出登录
        exit(){
            localStorage.clear();
            this.$router.push("/");
        },
        //提交用户信息修改
        changeName(){            
            if(this.username){
                Axios.postAxios("/api/changeuserdata",{
                    type : "name",
                    data : this.username
                },{
                    "Authorization" : "Bearer "+this.userToken
                }).then(res=>{
                     this.$message("修改成功");
                },err=>{
                     this.$message("修改失败，请检查网络或登录状态")
                })
            }else{
                 this.$message("修改名称不能为空")
            }
        },
        //提交用户电话修改
        changePhone(){            
            if(this.userphone){
                if(!(/^1[34578]\d{9}$/.test(this.userphone))){ 
                     this.$message("手机号码有误，请重填");  
                    return false; 
                } 
                Axios.postAxios("/api/changeuserdata",{
                    type : "phone",
                    data : this.userphone
                },{
                    "Authorization" : "Bearer "+this.userToken
                }).then(res=>{
                     this.$message("修改成功");
                },err=>{
                     this.$message("修改失败，请检查网络或登录状态")
                })
            }else{
                 this.$message("修改电话不能为空")
            }
        },
        //上传后的事件
        handleAvatarSuccess(res, file) {            
            this.imageUrl = URL.createObjectURL(file.raw);
             this.$message("上传成功");
        },
        //检测上传
        beforeAvatarUpload(file) {            
            const isJPG = file.type === 'image/jpeg';
            const isPNG = file.type === 'image/png';
            const isLt2M = file.size / 1024 / 1024 < 2;

            if (!isJPG && !isPNG) {
                this.$message.error('上传头像图片只能是 JPG或者PNG 格式!');
                return false;
            }
            if (!isLt2M) {
                this.$message.error('上传头像图片大小不能超过 2MB!');
                return false
            }                   
            console.log(file)
            this.file = file;
            return (isJPG||isPNG) && isLt2M;
        }
    }   
};
</script>
<style lang="stylus" scoped>

@import '~@/assets/css/me/third';
.upload{
    width: 0.6rem;
    height: 0.8rem;    
    position absolute;
    overflow hidden;
    left 6.48rem !important;
    opacity 0;
}
.el-switch{
    position absolute
    right .28rem
    top 0
    bottom 0
    margin auto
}
.a {
    text-align: right;
    img{
        width 100%
    }
}
.ts{
    position absolute
    left 6.48rem !important
    top 0
    bottom 0
    width .6rem
    height .6rem
    display inline-block
}
.send {
    text-align: center;
    color: #fc5155;
    font-size: 0.24rem;
    padding: 0 0.08rem;
    position: absolute;
    right: 0.26rem;
    line-height: 0.54rem;
    top: 0.1rem;
    border: 1px solid #fc5155;
    border-radius: 4px;
}
.order .b{
    text-align left 
    position: absolute;
    left: 2.1rem;
    font-size: 0.28rem;
    color: #333;
    height: 0.8rem;
    line-height: 0.8rem;
    width: 5rem;
    top: 0;
}
</style>



