<template>
	<div>
		<div class="banner">
			<div class="search">
				<div style="margin:0 auto;float:none" class="input">
					<i><img src="../../../static/images/search.png" /></i>
					<input v-model="searchInput" @blur.prevent="changeCount()" placeholder="请输入股票代码/名称" type="" name="" id="" />
					<div v-show="addInput.length != 0" class="search_div" style="z-index: 999;position: relative;">
						<ul>
							<li v-for="(item,index) in addInput" :key="index">
								<div @click="postSearch(item.code)">
									<span class="left">{{item.code | filtersName}}</span>
									<span style="margin-left:.2rem;" class="left">{{item.name}}</span>
								</div>
								<span @click="addMyself(item.code)" class="right">点击添加</span>
							</li>
						</ul>
					</div>
				</div>
			</div>
					<!-- <img style="width:100%" src="../../../static/images/banner_01.jpg" /> -->

			<swiper :options="swiperOption" ref="mySwiper">
				<swiper-slide>
					<img src="../../../static/images/banner_01.jpg" />
				</swiper-slide>
				<swiper-slide>
					<img src="../../../static/images/banner_02.jpg" />
				</swiper-slide>
				<div class="swiper-pagination" slot="pagination"></div>
			</swiper>
		</div>
		<div class="container">
			<div class="nav">
				<ul>
					<li>
						<router-link tag="a" to="/self">
							<img src="@s/images/nav_01.png" />
							<p>发布策略</p>
						</router-link>
					</li>
					<li>
						<router-link tag="a" to="/recharge">
							<img src="@s/images/nav_02.png" />
							<p>充值中心</p>
						</router-link>
					</li>
					<!-- <li>
						<router-link tag="a" to="/simBuy">						
							<img src="@s/images/nav_03.png" />
							<p>模拟点买</p>						
						</router-link>
					</li> -->
					<li class="nomargin">
						<router-link tag="a" to="mrouter">
							<img src="@s/images/nav_04.png" />
							<p>新手指引</p>
						</router-link>
					</li>
				</ul>
			</div>
			<div class="share_num">
				<ul>
					<li v-for="(item,index) in Zs" :key="index" class="li">
						<p class="top">{{item['name']}}</p>
						<p :class="{down_green:item.up_value.indexOf('-')!=-1}" style="color:red" class="center">{{item['current_price']}}</p>
						<div class="bottom">
							<span :class="{down_green:item.up_value.indexOf('-')!=-1}" style="color:red" class="left">{{item.up_value}}</span>
							<span :class="{down_green:item.up_value.indexOf('-')!=-1}" style="color:red" class="right">{{item.up_ratio}}</span>
						</div>
					</li>
				</ul>
			</div>
			<div class="comment">
				<router-link :to="moreLink" class="more">更多{{more}}&nbsp;&nbsp;&nbsp;&gt;</router-link>
				<div class="tabbar">
					<ul>
						<li v-for="(item,index) in tab" :key="index" @click="tabClick(index)" :class="{active:index == tabIndex}">{{item.title}}</li>
					</ul>
				</div>
				<!-- 更多动态 -->
				<div v-show="tabIndex == 0" class="tab">
					<ul>
						<li v-for="(item,index) in Dt" :key="index">
							<div class="dynamic">
								<div class="user">
									<span class="icon">
										<img style="border-radius:.18rem;width:.36rem;height:.36rem" :src="item.user.data.image" />
									</span>
									<span class="username">{{item.user.data.name}}</span>
								</div>
								<div class="transaction">
									<div class="user_num">
										{{item.shares.data.name}}&nbsp;&nbsp;{{item.shares.data.code | filtersName}}
									</div>
									<div v-if="item.price != null" class="buy">
										{{item.price}}&nbsp;&nbsp;<span>买入</span>
									</div>
									<div v-if="item.created_time!= null" class="time">
										{{item.created_time}} 
									</div>
								</div>
							</div>
						</li>
					</ul>
				</div>
				<!-- 最优策略 -->
				<div v-show="tabIndex == 1" class="tab2">
					<div v-for="(item,index) in Cl" :key="index" class="box">
						<div class="img">
							<img v-if="index<3" :src="'static/images/medal_0'+(index+1)+'.jpg'" alt="">
						</div>
						<div class="right">
							<div class="top">
								<p class="name">{{item.shares.data.name}}&nbsp;&nbsp;{{item.shares.data.code}}</p>
								<div class="user">
									<i class="icon">
										<img style="border-radius:.18rem;width:.36rem;height:.36rem" v-if="item.user.data.image!=null" :src="item.user.data.image" alt="">
									</i>
									<span>{{item.user.data.name?item.user.data.name:'时金用户'}}</span>
								</div>
							</div>
							<div class="center">
								<ul>
									<li><span :class="{down_green:parseInt(item.rate)<0}" class="first">{{item.rate}}</span></li>
									<li>
										<span class="title">买价:</span>
										<span class="num">{{item.price}}</span>
									</li>
									<li>
										<span class="title">卖价:</span>
										<span class="num">{{item.deal_price}}</span>
									</li>
									<li>
										<span class="title">收益率</span>
									</li>
									<li>
										<span class="title">盈亏:</span>
										<span class="num red" :class="{down_green:item.profit_money<0}">{{item.profit_money}}</span>
									</li>
									<li>
										<span class="title">数量: </span>
										<span class="num">{{item.num}}</span>
									</li>
								</ul>
							</div>
						</div>
					</div>
				</div>
				<!-- 最牛达人 -->
				<div v-show="tabIndex == 2" class="tab2">
					<div @click="toBull(item.id)" v-for="(item,index) in Bull" :key="index" class="box">
						<div class="img">
							<img v-if="index<3" :src="'static/images/medal_0'+(index+1)+'.jpg'" alt="">
						</div>
						<div class="right">
							<div style="margin-top: 0.7rem;margin-bottom:.2rem;line-height:1;" class="top">
								<p class="name">{{item.name?item.name:'时金用户'}}&nbsp;&nbsp;</p>
								<span v-show="item.is_follow" @click.stop="gz(item.id)" style="float : right" class="iconfont">&#xe67c;</span>
								<span v-show="!item.is_follow" @click.stop="gz(item.id)" style="float : right" class="iconfont">&#xe633;</span>
							</div>
							<div class="center">
								<ul>
									<li>
										<span class="title">策略数</span>
										<span class="num">{{item.tacticsnum}}</span>
									</li>
									<li>
										<span class="title">胜算率:</span>
										<span class="num">{{item.winrate}}</span>
									</li>
									<li>
										<span class="title">收益率: </span>
										<span class="num">{{item.yieldrate}}</span>
									</li>
								</ul>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import Axios from "../../common/js/getAxios";
export default {
  mounted() {
    this.getZs();
    this.getDt();
    this.getCl();
    this.getBull();
  },
  filters: {
    // 股票变成数字
    // filtersName(v){
    //   return v.substring(2);
    // },
    //转成百分数
    toPercentage(value) {
      let str = Number(value * 100).toFixed(2);
      str += "%";
      return str;
    },
    //过滤日期
    filterDate(value) {
      let arr = value.split("-");
      let tmp = arr[1] + "-" + arr[2];
      arr = tmp.split(":");
      tmp = arr[0] + ":" + arr[1];
      return tmp;
    }
  },
  watch: {
    searchInput(data) {
      Axios.axiosGet("/api/stock/search", { code: data }).then(
        res => {
          //这里因为需要重新渲染页面，所以需要使用push，不能直接赋值
          this.addInput = res.data.data;
          console.log(this.addInput);
        },
        err => {
          console.log(err);
        }
      );
    }
  },
  data() {
    return {
      searchInput: "",
      addInput: [],
      tab: [
        {
          title: "最新动态",
          more: "动态",
          moreLink: "/dynamic"
        },
        {
          title: "最优策略",
          more: "策略",
          moreLink: "/morecl"
        },
        {
          title: "最牛达人",
          more: "达人",
          moreLink: "/bullUser"
        }
      ],
      tabIndex: 0,
      swiperOption: {
        autoplay: true,
        loop: true,
        pagination: {
          el: ".swiper-pagination"
        }
      },
      //4个指数的数据
      Zs: [],
      //动态的数据
      Dt: [],
      //策略的数据
      Cl: [],
      //最佳牛人
      Bull: [],
      more: "动态",
      moreLink: "/dynamic"
    };
  },
  methods: {
    toBull(v) {
      this.$router.push({path:'/bullUser',query:{id:v}});
    },
    changeCount() {
      // this.addInput = [];
    },
    postSearch(code) {
      let userToken = localStorage.getItem("Authorization");
      Axios.postAxios(
        "/api/search",
        {
          code: code
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          if (res.status) {
            console.log("历史记录添加成功");
          }
          //跳转到对应的K线页面
          this.$router.push({ path: "/ks", query: { code: code } });
        },
        err => {
          console.log(err);
        }
      );
    },
    addMyself(code) {
      let userToken = localStorage.getItem("Authorization");
      Axios.postAxios(
        "/api/selfstock",
        {
          code: code
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          if (res.status) {
             this.$message("已添加到自选股票");
          }
        },
        err => {
          console.log(err);
        }
      );
    },
    getZs() {
      Axios.axiosGet(
        "/api/stock/getRealTimeInfos/sh000001,sz399006,sz399001",
        {},
        {}
      ).then(
        res => {
          this.Zs = res.data;
        },
        err => {
          console.log(err);
        }
      );
    },
    //获取动态数据
    getDt() {
      // 清空原先数据
      this.Dt = this.Dt.splice(0, -1);
      Axios.axiosGet("/api/tactics/home/new?include=user,shares", {}, {}).then(
        res => {
          console.log(res);
          // this.Dt = this.Dt.concat(this.Dt, res.data.data);
          this.Dt = res.data.data;
        },
        err => {
          console.log(err);
        }
      );
    },
    // 获取策略数据
    getCl() {
      //清空原先数据
      this.Cl = this.Cl.splice(0, -1);
      Axios.axiosGet("/api/tactics/home/best?include=user,shares", {}, {}).then(
        res => {
          console.log(res);
          this.Cl = res.data.data;
        },
        err => {
          console.log(err);
        }
      );
    },
    //获取牛人数据
    getBull() {
      let userToken = localStorage.getItem("Authorization");
      if (userToken) {
        var url = "/api/getbestinfo_login";
        Axios.axiosGet(
          url,
          {},
          {
            Authorization: "Bearer " + userToken
          }
        ).then(res => {
          console.log(res);
          this.Bull = res.data.data;
        });
      } else {
        var url = "/api/getbestinfo_nologin";
        Axios.axiosGet(url, {}).then(res => {
          console.log(res);
          this.Bull = res.data.data;
        });
      }
    },
    tabClick(index) {
      this.tabIndex = index;
      this.more = this.tab[index].more;
      this.moreLink = this.tab[index].moreLink;
      if (index == 0) {
        this.getDt();
      } else if (index == 1) {
        this.getCl();
      } else if (index == 2) {
        this.getBull();
      }
      console.log(this.tabIndex);
    },
    gz(id) {
      let userToken = localStorage.getItem("Authorization");
      Axios.postAxios(
        "/api/follow",
        {
          to_user_id: id
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          console.log(res);
          this.$message({
            message: res.data.msg,
            type: "success"
          });
          this.getBull();
        },
        err => {
          console.log(err);
        }
      );
    }
  }
};
</script>

<style lang="stylus" scoped>
@import '~@/assets/css/index';

</style>




