<template>
  <div>
    <return-top>
      <p slot="title">相关协议</p>
    </return-top>
    <div @click="getAgree()" class="box a">
      股票（交易合作协议）
    </div>
    <div v-if="a1" v-for="(item,index) in message" :key="index" class="box">
      <p class="title">{{item.title}}</p>
      <p class="txt" v-html="item.announcement">{{item.announcement}}</p>
    </div>
    <div @click="getAgree2()" class="box a">
      股票（交易居间合作协议）
    </div>

    <div v-if="a2" class="box">        
      <p class="title">{{message2.title}}</p>
      <p class="txt" v-html="message2.announcement"></p>
    </div>
  </div>
</template>

<script>
import ReturnTop from "@/components/ReturnTop";
import Axios from "../../common/js/getAxios";

export default {
  data() {
    return {
      a1: true,
      a2:false,
      message: [],
      message2: []
    };
  },
  mounted() {
    this.getAgree();
  },
  components: {
    ReturnTop
  },
  methods: {
      getAgree2() {
        this.a2 = !this.a2;
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosGet(
        "/api/payagreement/",
        {},
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
            this.message2 = res.data.msg;          
            console.log(this.message2)
        },
        err => {
          this.$message("请检查网络或登录状态");
        }
      );
    },
    getAgree() {
          this.a1 = !this.a1;
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosGet(
        "/api/agreement/",
        {},
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          console.log(res.data.msg);
          this.message = this.message.concat(res.data.msg);
        },
        err => {
          this.$message("请检查网络或登录状态");
        }
      );
    }
  }
};
</script>
<style lang="stylus" scoped>
.a {
  font-size: 0.4rem;
  line-height: 1rem;
  border-bottom: 1px solid #e4e4e4;
}
.box {
  padding: 0 0.3rem;
  &:before {
    content: '';
    display: table;
  }
  .title {
    margin-top: 0.36rem;
    font-size: 0.3rem;
    color: #303030;
    font-weight: bold;
  }
  .txt {
    font-size: 0.3rem;
    margin-top: 0.1rem;
    color: #303030;
  }
}
</style>





