<iframe frameborder="0" width="100%" height="100%" scrolling="yes" src="https://www.jin10.com/example/jin10.com.html?messageNum=50&fontSize=14px&theme=white"></iframe>

  </div>
</template>

<script>
import Axios from "../../common/js/getAxios";
import BScroll from "better-scroll";

export default {
  data() {
    return {
      //要闻
      mustNew: [],
      tabIndex: 0,
      tab: [
        {
          title: "要闻"
        },
        {
          title: "操盘必读"
        },
        {
          title: "行业研究"
        },
        {
          title: "新股快讯"
        },
        {
          title: "全球股市"
        }
      ]
    };
  },
  mounted() {

    this.getMessage(1);
    //实例化scroll并且可以点击
    this.scroll = new BScroll(this.$refs.wrapper, {
      click: "true",
      pullUpLoad: {
        threshold: 10
      }
    });
    this.lazyLoad();
  },
  methods: {
    //上拉加载更多
    lazyLoad() {
      //类似于一个可以操控dom事件的区域
      this.$nextTick(() => {
        // 上拉动作
        this.scroll.on("pullingUp", () => {
          console.log("run");
          this.getMessage();
        });
      });
    },
    //封装一个获取信息方法
    getMessage(type) {
      Axios.axiosGet("/api/articles/" + type, {}, {}).then(
        res => {
          this.mustNew = this.mustNew.concat(res.data.data);
        },
        err => {
           this.$message("信息获取失效，请刷新后重试");
        }
      );
    },
    tabClick(index) {
      this.tabIndex = index;
      this.mustNew.splice(0, this.mustNew.length);
      this.getMessage(++index);
    }
  }
};
</script>

<style lang="stylus" scoped>
@import '~@/assets/css/zx/zx';
iframe{
  top:0;
  bottom:0;
  position:absolute;
  // overflow-y:hidden;
}
</style>





