<template>
  <div>
    <return-top>
      <p slot="title">最新策略</p>
    </return-top>
    <div class="tab2">

      <div class="wrapper" ref="wrapper">
        <div class="scroll_container">
          <div v-for="(item,index) in Cl" :key="index" class="box">
            <div class="img">
              <img v-if="index<3" :src="'../../../static/images/medal_0'+(index+1)+'.jpg'" alt="">
            </div>
            <div class="right">
              <div class="top">
                <p class="name">{{item.shares.data.name}}&nbsp;&nbsp;{{item.shares.data.code}}</p>
                <div class="user">
                  <i class="icon">
                    <img style="border-radius:.18rem;width:.36rem;height:.36rem" v-if="item.user.data.image!=null" :src="item.user.data.image" alt="">
                  </i>
                  <span>{{item.user.data.name?item.user.data.name:'时金用户'}}</span>
                </div>
              </div>
              <div class="center">
                <ul>
                  <li><span :class="{down_green:parseInt(item.rate)<0}" class="first">{{item.rate}}</span></li>
                  <li>
                    <span class="title">买价:</span>
                    <span class="num">{{item.price}}</span>
                  </li>
                  <li>
                    <span class="title">卖价:</span>
                    <span class="num">{{item.deal_price}}</span>
                  </li>
                  <li>
                    <span class="title">收益率</span>
                  </li>
                  <li>
                    <span class="title">盈亏:</span>
                    <span class="num red" :class="{down_green:item.profit_money<0}">{{item.profit_money}}</span>
                  </li>
                  <li>
                    <span class="title">数量: </span>
                    <span class="num">{{item.num}}</span>
                  </li>
                </ul>
              </div>
            </div>
          </div>

        </div>
      </div>

    </div>
  </div>
</template>

<script>
import ReturnTop from "@/components/ReturnTop";
import Axios from "../../common/js/getAxios";
import BScroll from "better-scroll";

export default {
  mounted() {
    this.scroll = new BScroll(this.$refs.wrapper, {
      click: "true",
      pullUpLoad: {
        threshold: 10
      }
    });
    this.getCl();
    this.lazyLoad();
  },
  data() {
    return {
      page: 0,
      Cl: []
    };
  },
  components: {
    ReturnTop
  },
  methods: {
    //上拉加载更多
    lazyLoad() {
      //类似于一个可以操控dom事件的区域
      this.$nextTick(() => {
        // 上拉动作
        this.scroll.on("pullingUp", () => {
          console.log("run");
          this.getCl();
        });
      });
    },
    //获取动态数据
    getCl() {
      //清空原先数据
      // this.Dt = this.Dt.splice(0, -1);
      Axios.axiosGet(
        "/api/tactics/list/best?include=user,shares",
        {
          page: this.page
        },
        {}
      ).then(
        res => {
          console.log(res);
          this.Cl = this.Cl.concat(this.Cl, res.data.data);
          console.log(this.Cl);
          this.page++;
          //告诉betterscroll，老子已经渲染完了，用来解决只能下拉加载两次后不触发加载事件
          this.scroll.finishPullUp();
          //重新渲染scroll
          this.scroll.refresh();
        },
        err => {
          console.log(err);
        }
      );
    }
  }
};
</script>

<style lang="stylus" scoped>
@import '~@/assets/css/cl/more2.styl';
</style>




