<template>
  <div>
    <img style="width:100%" :src="ewm" alt="">
    <h1 style="width:96%;margin:.4rem auto;font-size:.3rem;text-align:center;">      
            <p style="color:#df411e" class="bottom">周一至周日（7*24小时实时入金）</p>
            <p style="color:#df411e" class="bottom">转账时请备注用户名和转账金额</p>
        </h1>
  </div>
</template>

<script>
import Axios from "../../common/js/getAxios";

export default {
  mounted() {
    this.getYhk();
  },
  data() {
    return {
      rnm: "",
      dg:"",
      ewm:""
    };
  },
  methods: {
    getYhk() {
      Axios.axiosGet(
        "/api/recharge/",
        {},
        {
          // Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          this.dg = res.data.dg_img;
          this.ewm = res.data.zfb_img;
          this.rnm = res.data;
        },
        err => {
          this.$message("请检查网络或登录状态");
        }
      );
    }
  }
};
</script>
<style lang="stylus" scoped>
.item{
    width:94%;    
    margin:.5rem auto;
    font-size:.4rem;
}
</style>



