export default {
    drawZx(myChart, code) {
        $.get('/api/stock/kline/hourly/' + code, function(data) {
            myChart.setOption({
                tooltip: {
                    trigger: 'axis',
                    formatter: '{c0}'
                },
                xAxis: {
                    // type: 'category',
                    // boundaryGap: false,
                    lineStyle: {
                        color: '#ff0000'
                    },
                    data: data.map(function(item) {
                        return item[0];
                    })
                },
                yAxis: {
                    splitLine: {
                        show: false
                    },
                    lineStyle: {
                        color: '#ff0000'
                    },
                    max: function(value) {
                        return value.max;
                    },
                    min: function(value) {
                        return value.min;
                    }
                },
                dataZoom: [{
                    start: 80,
                    show: false
                }, {
                    type: 'inside',

                }],
                series: [{
                        areaStyle: {
                            color: '#ff0000',
                            opacity: .5
                        },
                        name: 'Beijing AQI',
                        type: 'line',
                        lineStyle: {
                            color: '#ff0000'
                        },
                        data: data.map(function(item) {
                            return item[1];
                        })
                    },
                    {
                        //最下方小标
                        // name: "MA5",
                        type: "line",
                        data: "",
                        smooth: true,
                        lineStyle: {
                            normal: { opacity: 0.5 }
                        }
                    },
                    {
                        // name: "MA10",
                        type: "line",
                        data: "",
                        smooth: true,
                        lineStyle: {
                            normal: { opacity: 0.5 }
                        }
                    },
                    {
                        // name: "MA20",
                        type: "line",
                        data: "",
                        smooth: true,
                        lineStyle: {
                            normal: { opacity: 0.5 }
                        }
                    },
                    {
                        // name: "MA30",
                        type: "line",
                        data: "",
                        smooth: true,
                        lineStyle: {
                            normal: { opacity: 0.5 }
                        }
                    },
                    {
                        name: "Volume",
                        type: "bar",
                        xAxisIndex: 1,
                        yAxisIndex: 1,

                    }
                ]
            }, false);
        });
    }
}


