<template>
  <div>
    <div :style="{backgroundImage:'url('+ title_box +')'}" class="title_box">
      <div class="return" @click="back()">
        <div class="a">
          <span class="iconfont">&#xe6a0;</span>
        </div>
        <span class="txt">返回</span>
      </div>

      <div class="user_info">
        <div class="icon">
          <img :src="userinfo.image">
          <!-- {{userinfo}} -->
        </div>
        <div class="user">
          <p class="top">{{userinfo.name}}</p>
          <p class="bottom">{{userinfo.intro}}</p>
        </div>
        <div @click="is_see(userinfo.id)" class="see">
          <img v-if="isGz == 1" src='../../../static/images/nosee.png' alt="">
          <img v-else src='../../../static/images/see.png' alt="">
        </div>        
      </div> 

      <div class="more_news">
        <ul>
          <li>
            <p class="top">策略数</p>
            <p class="bottom">{{userinfo.tacticsnum}}</p>
          </li>
          <li>
            <p class="top">胜算率</p>
            <p class="bottom">{{userinfo.winsum/userinfo.tacticsnum | toPercentage}}</p>
          </li>
          <li>
            <p class="top">收益率</p>
            <p class="bottom">{{userinfo.yieldrate}}</p>
          </li>
          <li>
            <p class="top">粉丝数</p>
            <p class="bottom">{{userinfo.follower}}</p>
          </li>
        </ul>
      </div>
    </div>

  <div class="center_box">
    <div class="title_top">
      <div class="left">最新动态</div>
      <router-link :to="{path:'bullUserMore',query:{id:id}}" class="right">查看更多&gt;</router-link>
    </div>  
    <ul class="center">
      <li v-for="(item,index) in shareinfo" :key="index" class="item">
        <div class="top">
          <p class="left">{{item.name}}</p>
          <router-link tag="p" :to="{path:'bullCreate',query:{id:item.id,code:item.code}}" class="right">跟买</router-link>
        </div>
        <div class="bottom">
          <ul>
            <li class="left down_green">卖出: <span>{{item.num}} 股</span></li>
            <li class="right">价格：<span>{{item.price}} 元</span></li>
            <!-- <li class="left">单笔收益：<span class="up_red">30.03%</span></li> -->
            <li class="right">操作时间：<span>{{item.created_at}}</span></li>
          </ul>
        </div>
      </li>
    </ul>
  </div>


  </div>  
</template>

<script>
import Axios from "../../common/js/getAxios";
import title_box from "@/../static/images/title_box.jpg";

export default {
  filters: {

    toPercentage(value) {
      let str = Number(value * 100).toFixed(2);
      str += "%";
      return str;
    },

  },
  data() {
    return {
      isGz : "",
      title_box: title_box,
      id:this.$route.query.id,
      //用户信息
      userinfo : {},
      //股票信息
      shareinfo: []
    };
  },
  mounted(){
    this.getInit();    
  },
  methods : {
    back(){
      this.$router.go(-1);
    },
    is_see(id){
      let userToken = localStorage.getItem("Authorization");
      Axios.postAxios(
        "/api/follow",
        {
          to_user_id: id
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {
          if(this.isGz == 0){
            this.isGz = 1;
          }else{
            this.isGz = 0;
          }
          this.$message({
            message: res.data.msg,
            type: "success"
          });          
        },
        err => {
          console.log(err);
        }
      );
    },
    // 获取初始信息
    getInit(){
      let userToken = localStorage.getItem("Authorization");
      Axios.axiosGet(
        "/api/bestmaninfo",
        {
          to_user_id:this.id
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(
        res => {          
          console.log(res)
          this.userinfo = res.data.data.userinfo;
          this.shareinfo = res.data.data.getdynamicinfo.data;
          this.isGz = res.data.data.focus;
        },
        err => {}
      );
    }
  }
};
</script>

<style lang="stylus" scoped>
@import '~@/assets/css/home/bullUser';

</style>

 


