<template>
  <div>
    <return-top>
      <p slot="title">涨幅榜</p>
    </return-top>
    <div class="rank">
      <div class="title">
        <ul>
          <li class="first">名称代码</li>          
          <li class="secend">最新价</li>          
          <li class="third">涨幅</li>          
        </ul>
      </div>
      <div class="container">
        <ul>
          <li>
            <div class="name">
              <p class="top">嘉麟杰</p>
              <p class="bottom">002486</p>
            </div>
            <div class="num">3.58</div>
            <div class="percentage">+10.15%</div>
          </li>
          <li>
            <div class="name">
              <p class="top">嘉麟杰</p>
              <p class="bottom">002486</p>
            </div>
            <div class="num">3.58</div>
            <div class="percentage">+10.15%</div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import ReturnTop from "@/components/ReturnTop";
export default {
  components : {
    ReturnTop
  }
};
</script>
<style lang="stylus" scoped>
@import '~@/common/css/variable'
.rank {
  margin-bottom: 0.19rem solid #f4f4f4;
  .title {
    padding: 0 0.3rem;
    line-height: 0.8rem;
    @extend .noafter;
    font-size: 0.3rem;
    color: #303030;
    li{
      margin-top: 0.2rem;
      @extend .noafter;
      @extend .nobefore;
      height: 0.8rem;
      float: left;
      width: 33.333%;      
    }
    .first{
        text-align left
      }
      .secend{
        text-align center
      }
      .third{
        text-align right
      }
  }

  .iconfont {
    float: right;
  }

  .container {
    padding: 0 0.3rem;

    ul {
      @extend .noafter;

      li {
        margin-top: 0.2rem;
        @extend .noafter;
        @extend .nobefore;
        height: 0.8rem;

        div {
          float: left;
          width: 33.333%;
        }

        .name {
          .top {
            font-size: 0.3rem;
            color: #303030;
          }

          .bottom {
            font-size: 0.23rem;
            color: #9a9a9a;
          }
        }

        .num {
          text-align: center;
          color: #f6341a;
          line-height: 0.8rem;
          font-size: 0.3rem;
        }

        .percentage {
          line-height: 0.8rem;
          font-size: 0.3rem;
          color: #f6341a;
          text-align: right;
        }
      }
    }
  }
}
</style>




