<template>
  <div class="footer">
		<ul>      
      <!-- 备用激活方案  直接使用exact严格匹配-->
      <!-- :class="{active : $route.path.indexOf(item.link) != '-1'&&$route.path.indexOf(item.link) != '0' || $route.path == item.link}" -->
			<router-link class="li"  tag="li" active-class="active" :to="item.link" v-for="(item,index) in navbar" :key="index" exact>        
				<i v-if="$route.path.indexOf(item.link) != '-1'&&$route.path.indexOf(item.link) != '0' || $route.path == item.link" class="top iconfont" v-html="item.actIcon"></i>
				<i class="top iconfont" v-else v-html="item.icon"></i>
				<p class="bottom">{{item.title}}</p>
			</router-link>		
		</ul>
	</div>
</template>

<script>
export default {
  mounted(){
    console.log(this.$route.path)
  },
  data(){
    return { 
      //判断点击的索引值
      activeIndex : 0,  
      // 循环生成的导航
      navbar : [
        {
          title : "首页",
          icon : '&#xe63b;',
          actIcon : "&#xe65d;",
          link : "/"  
        },
        {
          title : "自选",
          icon : "&#xe62a;",
          actIcon : "&#xe64b;",
          link : "self"

        },
        {
          title : "策略",
          icon : "&#xe638;",
          actIcon : "&#xe64e;",
          link : "/cl"

        },
        {
          title : "资讯",
          icon : "&#xe631;",
          actIcon : "&#xe643;",
          link : "zx"

        },
        {
          title : "我的",
          icon : "&#xe63a;",
          actIcon : "&#xe65b;",
          link : "me"
        },
      ],
    }
  },
  methods : {
    tap(index){
      this.activeIndex = index;
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~@/assets/css/bottomFooter';
</style>



