<template>
  <div>
    <return-top>
      <p slot="title">最新动态</p>
    </return-top>
    <div class="wrapper" ref="wrapper">
      <div class="scroll_container">

        <div class="tab">
          <ul>
            <li v-for="(item,index) in Dt" :key="index">
              <div class="dynamic">
                <div class="user">
                  <span class="icon">
                    <img style="border-radius:.18rem;width:.36rem;height:.36rem" :src="item.user.data.image" />
                  </span>
                  <span class="username">{{item.user.data.name}}</span>
                </div>
                <div class="transaction">
                  <div class="user_num">
                    {{item.shares.data.name}}&nbsp;&nbsp;&nbsp;{{item.shares.data.code}}
                  </div>
                  <div v-if="item.price != null" class="buy">
                    {{item.price}}&nbsp;&nbsp;&nbsp;<span>买入</span>
                  </div>
                  <div v-if="item.user.data.updated_at!= null" class="time">
                    {{item.user.data.updated_at | filterDate}}
                    <!-- 09-27 10:50 -->
                  </div>
                </div>
              </div>
            </li>
          </ul>
        </div>

      </div>
    </div>

  </div>
</template>

<script>
import ReturnTop from "@/components/ReturnTop";
import Axios from "../../common/js/getAxios";
//better-scroll
import BScroll from "better-scroll";
export default {
  mounted() {
    this.getDt();
    this.scroll = new BScroll(this.$refs.wrapper, {
      click: "true",
      pullUpLoad: {
        threshold: 10
      }
    });
    this.lazyLoad();
  },
  data() {
    return {
      page : 0,
      Dt: []
    };
  },
  components: {
    ReturnTop
  },
  filters: {
    //过滤日期
    filterDate(value) {
      let arr = value.split("-");
      let tmp = arr[1] + "-" + arr[2];
      arr = tmp.split(":");
      tmp = arr[0] + ":" + arr[1];
      return tmp;
    }
  },
  methods: {
    //上拉加载更多
    lazyLoad() {
      //类似于一个可以操控dom事件的区域
      this.$nextTick(() => {
        // 上拉动作
        this.scroll.on("pullingUp", () => {
          console.log("run");
          this.getDt();
        });
      });
    },
    //获取动态数据
    getDt() {
      //清空原先数据
      // this.Dt = this.Dt.splice(0, -1);
      Axios.axiosGet("/api/tactics/list/new?include=user,shares", {
        page : this.page
      }, {}).then(
        res => {
          this.Dt = this.Dt.concat(this.Dt, res.data.data);
          this.page++;
          //告诉betterscroll，老子已经渲染完了，用来解决只能下拉加载两次后不触发加载事件
          this.scroll.finishPullUp()
          //重新渲染scroll
          this.scroll.refresh();          
        },
        err => {
          console.log(err);
        }
      );
    }
  }
};
</script>


<style lang="stylus" scoped>
@import '~@/assets/css/cl/more';
</style>




