import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/Index'

//底部5个切换
import Home from '@/pages/home/Home'
import Me from '@/pages/me/Me'
import Cl from '@/pages/cl/Cl'
  //自选
import Self from '@/pages/self/Self'
import CreateSelf from '@/pages/self/Create'
import SelfMore from '@/pages/self/More'
import Zx from '@/pages/zx/Zx'  
import ZxMores from '@/pages/zx/Mores'

//新手教程
import Mrouter from '@/pages/login/Router'

// 首页更多策略/动态页面
import Dynamic from '@/pages/cl/More'
import MoreCl from '@/pages/cl/More2'

//充值
import Recharge from '@/pages/me/Recharge'
import RechargeFirst from '@/pages/me/First'
import RechargeSecend from '@/pages/me/Secend'
import RechargeThird from '@/pages/me/Third'
import ChargeNav from '@/pages/me/Nav'
import ChargeXs from '@/pages/me/Xs'
import ChargeZfb from '@/pages/me/Zfb'
import ChargeDg from '@/pages/me/Dg'

//提现
import Charge from '@/pages/me/Charge'
import ChargeFirst from '@/pages/me/ChargeFirst'

//协议
import AgreeMent from '@/pages/me/AgreeMent'

//个人设置
import Setting from '@/pages/me/Setting'
import SettingName from '@/pages/me/SettingName' 
import Modify from '@/pages/me/Modify'
import Mymoney from '@/pages/me/Mymoney'
import Adv from '@/pages/me/Adv'

//登录注册
import LoginRegistrat from '@/pages/login/LoginRegistrat'
import Login from '@/pages/login/Login'
import Forget from '@/pages/login/Forget'
import Registrat from '@/pages/login/Registrat'

//K线
import Ks from '@/pages/ks/Ks'
import K from '@/pages/ks/K'

//支付协议
import PayMessage from '@/pages/pay/Message'

//模拟点买
import SimBuy from "@/pages/home/SimBuy";
import SimCreate from '@/pages/self/SimCreate'

//牛人
import BullUser from '@/pages/home/BullUser'
import BullUserMore from '@/pages/home/Bull'
import FollowBull from '@/pages/home/FollowBull'
import BullCreate from "@/pages/self/BullCreate";
// 客服
import Kf from "@/pages/me/Kf";

//公告
import OutNotice from "@/pages/notice/OutNotice";
import InNotice from '@/pages/notice/InNotice'

//实名认证
import Rz from '@/pages/me/Rz'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: "/kf",
      component: Kf
    },
    {
      path:"/rz",
      component:Rz
    },
    //公告
    {
      path: "/outNotice",
      component: OutNotice
    },
    {
      path: "/inNotice",
      component: InNotice
    },
    //牛人
    {
      path: "/bullUser",
      component: BullUser
    },
    //更多牛人
    {
      path: "/bullUserMore",
      component: BullUserMore
    },
    {
      path: "/bullCreate",
      component: BullCreate
    },
    //关注牛人
    {
      path: "/followBull",
      component: FollowBull
    },
    //模拟点买
    {
      path: "/simBuy",
      component: SimBuy
    },
    {
      path: "/simCreate",
      component: SimCreate
    },
    //支付协议
    {
      path: "/payMessage",
      component: PayMessage
    },
    //k
    {
      path: "/ks",
      component: Ks,
      children: [
        {
          path: "",
          component: K
        }
      ]
    },
    //新手教程
    {
      path: "/mrouter",
      component: Mrouter
    },
    //登录
    {
      path: "/loginRegistrat",
      component: LoginRegistrat,
      children: [
        {
          path: "",
          component: Login
        },
        {
          path: "forget",
          component: Forget
        },
        {
          path: "registrat",
          component: Registrat
        }
      ]
    },
    {
      path: "/",
      // name: 'Index',
      component: Index,
      children: [
        {
          path: "me",
          name: "Me",
          component: Me
        },
        {
          path: "cl",
          name: "Cl",
          component: Cl
        },
        {
          path: "self",
          name: "Self",
          component: Self
        },
        {
          path: "zx",
          name: "Zx",
          component: Zx
        },
        {
          path: "",
          component: Home
        }
      ]
    },
    {
      path: "/createSelf",
      name: "CreateSelf",
      component: CreateSelf
    },
    {
      path: "/selfMore",
      name: "SelfMore",
      component: SelfMore
    },
    {
      path: "/dynamic",
      name: "Dynamic",
      component: Dynamic
    },
    {
      path: "/morecl",
      name: "MoreCl",
      component: MoreCl
    },
    {
      path: "/recharge",
      // name : 'Recharge',
      component: Recharge,
      children: [
        {
          path: "",
          component: RechargeFirst,
          children:[
            {
              path:'',
              component:ChargeNav
            },
            {
              path:'xs',
              component:ChargeXs
            },
            {
              path:'zfb',
              component:ChargeZfb
            },
            {
              path: 'dg',
              component: ChargeDg
            }
          ]
        },
        {
          path: "secend",
          name: "RechargeSecend",
          component: RechargeSecend
        },
        {
          path: "third",
          name: "RechargeThird",
          component: RechargeThird
        }
      ]
    },
    {
      path: "/charge",
      // name : 'Charge',
      component: Charge,
      children: [
        {
          path: "",
          component: ChargeFirst
        }
      ]
    },
    {
      path: "/setting",
      // name : 'Setting',
      component: Setting,
      children: [
        {
          path: "",
          component: SettingName
        },
        {
          path: "modify",
          name: "Modify",
          component: Modify
        }
      ]
    },
    {
      path: "/agreeMent",
      name: "AgreeMent",
      component: AgreeMent
    },
    {
      path: "/myMoney",
      name: "Mymoney",
      component: Mymoney
    },
    {
      path: "/adv",
      name: "Adv",
      component: Adv
    },
    {
      path: "/zxMores",
      name: "ZxMores",
      component: ZxMores
    }
  ]
});



