export default {
  drawLine(myChart,type,code) {      
    // 绘制图表
    var upColor = "#e3605e";
    var downColor = "#90cc92";
    function splitData(rawData) {
      var categoryData = [];
      var values = [];
      var volumes = [];
      for (var i = 0; i < rawData.length; i++) {
        categoryData.push(rawData[i].splice(0, 1)[0]);
        values.push(rawData[i]);
        volumes.push([
          i,
          rawData[i][4],
          rawData[i][0] > rawData[i][1] ? 1 : -1
        ]);
      }
      return {
        categoryData: categoryData,
        values: values,
        volumes: volumes
      };
    }

    function calculateMA(dayCount, data) {
      var result = [];
      for (var i = 0, len = data.values.length; i < len; i++) {
        if (i < dayCount) {
          result.push("-");
          continue;
        }
        var sum = 0;
        for (var j = 0; j < dayCount; j++) {
          sum += data.values[i - j][1];
        }
        result.push(+(sum / dayCount).toFixed(3));
      }
      return result;
    }
    // /test/data/asset/data/stock-DJI.json
    $.get("/api/stock/kline/"+type+"/"+code,function(rawData) {
    // $.get("/test/data/asset/data/stock-DJI.json",function(rawData) {
      // var data = splitData(rawData);
      var data = splitData(rawData);
      myChart.setOption(
        ({
          backgroundColor: "#fff",
          animation: false,
          legend: {
            bottom: 10,
            left: "50%",
            data: ["Dow-Jones index", "MA5", "MA10", "MA20", "MA30"]
          },
          tooltip: {
            trigger: "axis",
            axisPointer: {
              type: "cross"
            },
            backgroundColor: "rgba(245, 245, 245, 0.8)",
            borderWidth: 1,
            borderColor: "#ccc",
            padding: 10,
            textStyle: {
              color: "#000"
            },
            position: function(pos, params, el, elRect, size) {
              var obj = { top: 10 };
              obj[["left", "right"][+(pos[0] < size.viewSize[0] / 2)]] = 30;
              return obj;
            }
          },
          axisPointer: {
            link: { xAxisIndex: "all" },
            label: {
              backgroundColor: "#777"
            }
          },
          visualMap: {
            show: false,
            seriesIndex: 5,
            dimension: 2,
            pieces: [
              {
                value: 1,
                color: downColor
              },
              {
                value: -1,
                color: upColor
              }
            ]
          },
          grid: [
            {
              top : "10%",
              left: "8%",
              right: "3%",
              height: "80%"
            },
            {
              left: "12%",
              right: "3%",
              bottom: "0%",
              height: "0%"
            }
          ],
          xAxis: [
            {
              type: "category",
              data: data.categoryData,
              scale: true,
              boundaryGap: false,
              axisLine: { onZero: false },
              splitLine: { show: false },
              splitNumber: 20,
              min: "dataMin",
              max: "dataMax",
              axisPointer: {
                z: 100
              }
            },
            {
              type: "category",
              gridIndex: 1,
              data: data.categoryData,
              scale: true,
              boundaryGap: false,
              axisLine: { onZero: false },
              axisTick: { show: false },
              splitLine: { show: false },
              axisLabel: { show: false },
              splitNumber: 20,
              min: "dataMin",
              max: "dataMax"
            }
          ],
          yAxis: [
            {
              scale: true,
              splitArea: {
                show: true
              }
            },
            {
              splitArea: {
                show: false
              },
              scale: true,
              gridIndex: 1,
              splitNumber: 2,
              axisLabel: { show: false },
              axisLine: { show: false },
              axisTick: { show: false },
              splitLine: { show: false }
            }
          ],
          //缩放
          dataZoom: [
            {
              type: "inside",
              xAxisIndex: [0, 1],
              start: 0,
              end: 100
            },
          ],
          series: [
            {
              type: "candlestick",
              data: data.values,
              itemStyle: {
                normal: {
                  color: upColor,
                  color0: downColor,
                  borderColor: null,
                  borderColor0: null
                }
              },
              tooltip: {
                formatter: function(param) {
                  param = param[0];
                  return [
                    "Date: " +
                      param.name +
                      '<hr size=1 style="margin: 3px 0">',
                    "Open: " + param.data[0] + "<br/>",
                    "Close: " + param.data[1] + "<br/>",
                    "Lowest: " + param.data[2] + "<br/>",
                    "Highest: " + param.data[3] + "<br/>"
                  ].join("");
                }
              }
            },
            {
              //最下方小标
              // name: "MA5",
              type: "line",
              data: calculateMA(5, data),
              smooth: true,
              showSymbol: false,
              lineStyle: {
                normal: { opacity: 0.5 }
              }
            },
            {
              // name: "MA10",
              type: "line",
              data: calculateMA(10, data),
              smooth: true,
              showSymbol: false,
              lineStyle: {
                normal: { opacity: 0.5 }
              }
            },
            {
              // name: "MA20",
              type: "line",
              data: calculateMA(20, data),
              smooth: true,
              showSymbol: false,
              lineStyle: {
                normal: { opacity: 0.5 }
              }
            },
            {
              // name: "MA30",
              type: "line",
              data: calculateMA(30, data),
              smooth: true,
              showSymbol: false,
              lineStyle: {
                normal: { opacity: 0.5 }
              }
            },
            {
              name: "Volume",
              type: "bar",
              showSymbol: false,
              xAxisIndex: 1,
              yAxisIndex: 1,
              data: data.volumes,
              // show : false
            }
          ]
        }),
        true
      );
    });
  }
}


