<template>
  <div>
    <return-top>
      <p slot="title">{{codeData.name}}</p>
    </return-top>
    <div class="days">
      <div class="box1">
        <div class="left">
          <div :class="{down_green:(codeData.current_price-codeData.yesterday_end_price).toFixed(2)<0}" class="top">{{codeData.current_price}}</div>
          <div :class="{down_green:(codeData.current_price-codeData.yesterday_end_price).toFixed(2)<0}" class="bottom">{{(codeData.current_price-codeData.yesterday_end_price).toFixed(2)}} {{(codeData.current_price-codeData.yesterday_end_price)/codeData.yesterday_end_price | toPercentage}}</div>
        </div>
        <div class="right">
          <ul>
            <li>
              <p class="top">昨收</p>
              <p class="bottom">{{codeData.yesterday_end_price}}</p>
            </li>
            <li class="nomargin">
              <p class="top">今开</p>
              <p class="bottom">{{codeData.today_begin_price}}</p>
            </li>
            <li>
              <p class="top">最低</p>
              <p class="bottom">{{codeData.low}}</p>
            </li>
            <li class="nomargin">
              <p class="top">最高</p>
              <p class="bottom">{{codeData.high}}</p>
            </li>
          </ul>
        </div>
      </div>
      <div class="box2">
        <ul>
          <li>振 幅 <span>{{codeData.zhenfu}}%</span></li>
          <li>成交量 <span>{{codeData.volume}}</span></li>
          <li>成交额 <span>{{codeData.turnover}}</span></li>
          <li>内 盘 <span>{{codeData.inpan}}</span></li>
          <li>外 盘 <span>{{codeData.outpan}}</span></li>
          <li>总市值 <span>{{codeData.market_value}}</span></li>
          <li>市盈率 <span>{{codeData.PEratio}}</span></li>
          <li>市净率 <span>{{codeData.PBratio}}</span></li>
          <li>流通市值 <span>{{codeData.circulation_market_value}}</span></li>
        </ul>
      </div>
    </div>
    <div class="ktab">
      <ul>
        <li @click="changeKs(index)" v-for="(item,index) in ks" :key="index" :class="{active : index==KsIndex}">{{item}}</li>    
      </ul>
    </div>
    <div id="myChart" :style="{width: '7.5rem', height: '4rem'}"></div>

<!--
    <div class="newsTab">
      <ul>
        <li @click="newsClick(index)" v-for="(item,index) in News" :key="index" :class="{active : index==NewsIndex,noborder : index == 3}">{{item}}</li>    
      </ul>
    </div>

    <div v-show="NewsIndex == 0" class="tabli">
      <div class="box">
        <p class="top">长沙银行、兴瑞科技9月26上市</p>
        <div class="bottom">
          <p class="left">国海证券</p>
          <p class="right">2018-10-08 16:38:27</p>
        </div>
      </div>
      <div class="box">
        <p class="top">长沙银行、兴瑞科技9月26上市</p>
        <div class="bottom">
          <p class="left">国海证券</p>
          <p class="right">2018-10-08 16:38:27</p>
        </div>
      </div>
    </div>

    <div v-show="NewsIndex == 2" class="tabli">
      <div class="pie_box">
        <div id="myChart2" :style="{width: '7.5rem', height: '4rem'}"></div>
        <div class="inner">
          <p class="top">今日资金</p>
          <p class="bottom">Capital Flow</p>
        </div>
      </div>

      <p class="pietitle">
        最近5日主力增减仓
      </p>

      <div class="bar_graph">
        <ul>
          <li v-for="(item,index) in barGraph" :key="index">
            <p :class="{txt_red : item.num>0}" class="top">{{item.num}}</p>
            <p :class="{bg_red : item.num>0}" :style="{height : Math.abs(item.num)/barGraphMax*2 + 'rem'}" class="center"></p>
            <p class="bottom">{{item.date}}</p>
          </li>
        </ul>
        <div class="bottom">
          <div class="more">
            <i class="bg_red"></i>
            <span>资金流入</span>
          </div>
          <div class="more">
            <i></i>
            <span>资金流出</span>
          </div>
          <div class="right">
            单位(万元)
          </div>
        </div>
      </div>

    </div>
    -->

    <div class="bottom_click">
      <!-- <div class="left">删除自选</div> -->
      <!-- <div class="right">创建策略</div> -->
      <!-- 将code传递过去 -->
      <router-link v-if="isSim == 1" style="width:100%" tag="div" :to="{path:'/simCreate/',query : {code:code}}" class="right">创建策略</router-link>
      <router-link v-else style="width:100%" tag="div" :to="{path:'/createSelf/',query : {code:code}}" class="right">创建策略</router-link>
    </div>

  </div>
</template>

<script>
import ReturnTop from "@/components/ReturnTop";
import Axios from "../../common/js/getAxios";
import echart from './echart.js'
import pie from './pie.js'
import zx from './zx.js'
export default {
  filters : {
        //转成百分数
    toPercentage(value) {
      let str = Number(value * 100).toFixed(2);
      str += "%";
      return str;
    }
  },
  data(){
    return {
      interval:"",
      isSim:this.$route.query.isSim,
      //代码数据
      codeData : {},
      //上个页面传递过来的股票代码,通过这个查询更多信息
      code: this.$route.query.code,
      // 柱状图最大数
      barGraphMax : 7000,
      //柱状图数据
      barGraph : [
        {
          num : 188.0,          
          date : "09-26"
        },
        {
          num : -1022.0,
          date : "09-27"
        },
        {
          num : -529.0,
          date : "09-28"
        },
        {
          num : -3355.9,
          date : "10-01"
        },
        {
          num : 5053.4,
          date : "10-20"
        },
      ],
      KsIndex : 1,
      ks : ["分时","日K","周K","月K"],
      NewsIndex : 0,
      News : ["新闻","公告","资金","研报"],
      loadingPie : true
    }
  },  
  mounted(){
    // this.drawZx();
    this.drawLine("daily");
    this.getCodeData();
    console.log(this.isSim)
  },
  beforeDestroy () {
            window.clearInterval(this.interval);

},
  methods: {
    // 实时股票数据
    getCodeData(){
      Axios.axiosGet("/api/stock/realTimeInfo/"+this.code, {}).then(
        res => {          
          this.codeData = res.data;
        },
        err => {          
        }
      );
    },
    //更改K线类别
    changeKs(index){
      this.KsIndex = index;
      if(index == 1){
        window.clearInterval(this.interval);
        this.drawLine("daily");
      }else if(index == 2){
        window.clearInterval(this.interval);
        this.drawLine("weekly");
      }else if(index == 3){
        window.clearInterval(this.interval);
        this.drawLine("monthly");
      }else if(index == 0){
        this.interval = window.setInterval(()=>{
          this.drawZx();
          this.getCodeData();
        },60000)
      }
    },
    drawZx(){
      let myChart = this.$echarts.init(document.getElementById("myChart"));
      zx.drawZx(myChart,this.code);
      console.log(1);
    },
    drawLine(type){
      let myChart = this.$echarts.init(document.getElementById("myChart"));
      echart.drawLine(myChart,type,this.code);
    },
    newsClick(index){
      this.NewsIndex = index;          
      if(index == 2){
        if(this.loadingPie){
          this.drawPies();
        }
        this.loadingPie = false;
      }
    },
    drawPies(){
      let myChart = this.$echarts.init(document.getElementById("myChart2"));
      pie.drawPie(myChart);
    },
  },
  components: {
    ReturnTop
  }
};
</script>
<style lang="stylus" scoped>
@import '~@/assets/css/ks/k';
</style>



