<template>
    <div>
        <return-top>
            <p slot="title">资讯详情</p>
        </return-top>
        <div class="author_box">
            <p class="h1">{{news.title}}</p>
            <p class="more">
                <span>{{news.created_at}}</span>
                <span class="red">{{news.source}}</span>
            </p>
        </div>
        <div v-html="news.announcement" class="box">
        </div>
    </div>
</template>

<script>
import ReturnTop from "@/components/ReturnTop";
import Axios from "../../common/js/getAxios";

export default {
  data() {
    return {
      id: this.$route.query.id,
      news: {}
    };
  },
  mounted() {
    this.getMessage(this.id);
  },
  methods: {
    getMessage(id) {
      Axios.axiosGet("/api/article/" + id, {}, {}).then(
        res => {
          this.news = res.data.data;
        },
        err => {
           this.$message("信息获取失效，请刷新后重试");
        }
      );
    }
  },
  components: {
    ReturnTop
  }
};
</script>
<style lang="stylus" scoped>
.author_box {
    padding: 0.44rem 0.3rem 0.34rem;
    border-bottom: 1px solid #d5d5d4;

    .h1 {
        // text-align center
        margin-bottom: 0.22rem;
        font-size: 0.42rem;
        font-weight: bold;
        color: #303030;
    }

    .more {
        font-size: 0.24rem;
        line-height: 0.4rem;
        color: #909090;

        .red {
            font-size: 0.28rem;
            color: #f6341a;
        }
    }
}

.box {
    padding: 0 0.3rem;
    text-indent: 2em;
    font-size: 0.3rem;
    color: #303030;
    font-weight: bold;
    margin-top: 0.36rem;
    line-height: 1.7;
}
</style>





