<template>
    <div>
        <return-top>
          <p slot="title">推广</p>
        </return-top>

        <div class="order">
            <span class="qes">手机号码</span>
            <input type="text" placeholder="请输入推荐人手机号码" class="a"> 
        </div>

        <div class="order">
            <span class="qes">验证码</span>
            <input type="text" placeholder="请输入验证码" class="a"> 
        </div>

        <div class="order">
            <span class="qes">机构编码</span>
            <input type="text" placeholder="默认选择：9999" class="a"> 
        </div>

        <div class="order">
            <span class="qes">验证码</span>
            <input type="text" placeholder="请在输入验证码" class="a">
            <span class="send">获取验证码</span> 
        </div>

        <div @click="submitNext()" class="submit">
            提交申请
        </div>
    </div>
</template>

<script>
import ReturnTop from "@/components/ReturnTop";
export default {
    data(){
        return {
            value3 : true
        }
    },
  components: {
    ReturnTop
  }
};
</script>
<style lang="stylus" scoped>

@import '~@/assets/css/me/third';
.send{
    text-align center
    color #fc5155
    font-size .24rem
    padding .08rem;
    position absolute
    right .26rem
    line-height .54rem
    top .15rem
    border 1px solid #fc5155
    border-radius 4px
}
.el-switch{
    position absolute
    right .28rem
    top 0
    bottom 0
    margin auto
}
.a {
    // text-align: right;
    line-height 1rem !important
    height 1rem !important
    img{
        width 100%
    }
}
.ts{
    position absolute
    left 6.48rem !important
    top 0
    bottom 0
    width .6rem
    height .6rem
    display inline-block
}
.order{
    line-height 1rem
}
</style>



